import { SortOrder } from '.';

export enum PlaylistSortField {
    DisplayName = 'name',
    ModifiedDate = 'modifiedDate',
}

export interface PlaylistSearchParameters {
    searchTerms: string;
    searchLimit: number;
    folderId: string;
    sortField: PlaylistSortField;
    sortOrder: SortOrder;
}

export function getDefaultPlaylistSearchParameters() {
    return {
        searchTerms: '',
        searchLimit: 30,
        sortField: PlaylistSortField.ModifiedDate,
        sortOrder: SortOrder.Descending,
    };
}


