import * as React from 'react';
import { HashRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { LoadingProgress } from './components/common';
import { MiniExperiences } from './components/mini-experiences';
import { MiniPlaylists } from 'components/mini-playlists';
import { MiniVideos } from './components/mini-videos';

import { ConnectorContext } from './services/connector';
import * as URI from 'urijs';

import './Mini.css';

export class Mini extends React.Component<MiniProps, MiniState> {
    constructor(props: MiniProps, context?: any) {
        super(props, context);

        const uri = URI();
        const info = uri.query(true) as any;
        
        this.state = {
            isLoading: true,
            isDialog: info.IsDlg === '1' ? true : false
        };
    }

    async componentDidMount() {
        const { connectorContext } = this.props;

        this.setState({
            isLoading: true,
        });

        await connectorContext.initialize();

        this.setState({
            isLoading: false,
        });
    }

    render() {
        const { connectorContext } = this.props;
        const { isLoading, isDialog } = this.state;

        let miniStyle: React.CSSProperties = {
            width: '580px'
        };

        if (isDialog) {
            miniStyle.width = '';
            miniStyle.backgroundColor = 'initial';
        }

        if (isLoading) {
            return (
                <div className="mini" style={miniStyle}>
                    <LoadingProgress loadingMessage="Loading Brightcove Mini..." />
                </div>
            );
        }

        return (
            <Router>
                <div className="mini" style={miniStyle}>
                    <Switch>
                        <Route path="/mini/videos" exact={true}>
                            <MiniVideos connectorContext={connectorContext} />
                        </Route>
                        <Route path="/mini/playlists" exact={true}>
                            <MiniPlaylists connectorContext={connectorContext} />
                        </Route>
                        <Route path="/mini/experiences" exact={true}>
                            <MiniExperiences connectorContext={connectorContext} />
                        </Route>
                        <Redirect to="/" />
                    </Switch>
                </div>
            </Router>
        );
    }
}

interface MiniProps {
    connectorContext: ConnectorContext;
    raiseError: (message: string, err?: Error) => boolean;
}

interface MiniState {
    isLoading: boolean;
    isDialog: boolean;
}



