import { SPList } from './SPList';
import { SPListDefinition } from './SPListDefinition';
import { SPUtility } from '.';

import { find } from 'lodash';

export interface Account {
    id: string;
    guid: string;
    title: string;
    defaultVideoPlayerId: string;
    defaultPlaylistPlayerId: string;
    accountId: string;
    clientId: string;
    clientSecret: string;
    accountAuthorsGroupName: string;
    accountViewersGroupName: string;
    customFields?: Array<{ required: boolean, internalName: string, displayName: string }>;
}

export const AccountsListDefinition: SPListDefinition = {
    listName: 'Accounts',
    itemName: 'Account',
    itemNamePlural: 'Accounts',
    fieldMap: {
        Id: { path: 'id', type: 'text', create: false },
        GUID: { path: 'guid', type: 'text', create: false },
        Title: { path: 'title', type: 'text', create: false },
        DefaultVideoPlayerId: { path: 'defaultVideoPlayerId', type: 'text', create: true },
        DefaultPlaylistPlayerId: { path: 'defaultPlaylistPlayerId', type: 'text', create: true },
        AccountId: { path: 'accountId', type: 'text', create: true },
        ClientId: { path: 'clientId', type: 'text', create: true },
        ClientSecret: { path: 'clientSecret', type: 'text', create: true },
        AuthorsGroupID: { path: 'authorsGroupId', type: 'text', create: true },
        AccountAuthorsGroupName: { path: 'accountAuthorsGroupName', type: 'text', create: true },
        ViewersGroupID: { path: 'viewersGroupId', type: 'text', create: true },
        AccountViewersGroupName: { path: 'accountViewersGroupName', type: 'text', create: true },
        Tokens: { path: 'customFields', type: 'multiline-text', create: true },
    }
};

export class AccountsList extends SPList<Account> {
    constructor(hostWebUrl: string, addInWebUrl?: string, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
        super(AccountsListDefinition, hostWebUrl, addInWebUrl, errorCallback);
    }

    public async getAccountByAccountId(accountId: string) {
        if (!accountId) {
            throw new Error('accountId must be specified.');
        }

        const accounts = await this.getItemsByFieldValue('AccountId', accountId);

        if (accounts.length > 1) {
            throw new Error(`There are multiple account entries with the id '${accountId}'`);
        } else if (accounts.length < 1) {
            throw new Error(`There are no accounts with the id '${accountId}'`);
        }

        return accounts[0];
    }

    public async getDefaultAccount(): Promise<Account | undefined> {
        let items = await this.getItems();

        // TODO: Make this use a field rather than picking off the first account.
        if (items && items.length > 0) {
            return items[0];
        }

        return undefined;
    }
    
    mapToObject(listItem: any): Account {
        let account = super.mapToObject(listItem);

        // Map the interesting tokens field, items are comma seperated, fields are pipe seperated.
        if (account.customFields) {
            let tokenString = account.customFields as any as string;
            account.customFields = [];

            let customFields = tokenString.split(',');
            for (let customField of customFields) {
                let values = customField.split('|');
                account.customFields.push({
                    required: (values[0] === 'true'),
                    internalName: values[1],
                    displayName: values[2]
                });
            }
        }
        return account;
    }

    mapToListItem(item: Account): any {
        let listItem = super.mapToListItem(item);

        // Map the interesting tokens field, items are comma seperated, fields are pipe seperated.
        if (item.customFields) {
            let tokenStrings = [];
            for (let token of item.customFields) {
                tokenStrings.push(`${token.required}|${token.internalName}|${token.displayName}`);
            }
            listItem.Tokens = tokenStrings.join(',');
        } else {
            listItem.Tokens = '';
        }

        // Some funky logic here.
        listItem.AuthorsGroupID = item.accountAuthorsGroupName;
        listItem.ViewersGroupID = item.accountViewersGroupName;
        return listItem;
    }
}



