import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import autobind from 'autobind-decorator';

import { ConnectorContext, ConnectorValidationResult, UserRole } from './services/connector';

import { Home } from './components/home';
import { AccountManagement, AccountView } from './components/account-management';
import { AddVideos } from './components/add-videos';
import { ManageVideos, EditVideo } from './components/manage-videos';
import { ManagePlaylists, EditPlaylist } from './components/manage-playlists';
import { ManageExperiences, PreviewExperience } from './components/manage-experiences';
import { LoadingProgress } from './components/common';
import { InstallationValidation } from './components/installation-validation';

import './Manage.css';

const RouteButton = ({ label, to, activeOnlyWhenExact }: { label: string, to: string, activeOnlyWhenExact?: boolean }) => (
    <Route
        path={to}
        exact={activeOnlyWhenExact}
        children={({ match }) => (
            <Button bsSize="large" block={true} href={'#' + to} bsStyle={match ? 'primary' : 'default'}>
                {label}
            </Button>
        )}
    />
);

export class Manage extends React.Component<ManageProps, ManageState> {
    async componentDidMount() {
        const { connectorContext } = this.props;

        this.setState({
            isLoading: true,
        });

        let validationResult: ConnectorValidationResult = {
            isValid: false,
            isSettingsListCreated: false,
            isProxyDefined: false,
            isAccountsListCreated: false,
            areAccountsAvailable: false,
            isVideosListCreated: false,
            accountEmptyAccountId: [],
            accountEmptyGroups: [],
            missingGroups: [],
            currentUserAvailableAccounts: [],
            currentUserMaxRole: UserRole.None,
        };

        validationResult = await connectorContext.validateConfiguration();
        connectorContext.currentUserRole = validationResult.currentUserMaxRole;

        if (validationResult.isValid) {
            await connectorContext.initialize();
        }

        this.setState({
            isLoading: false,
            validationResult: validationResult,
            showInstallationModal: !validationResult.isValid,
        });
    }

    @autobind
    hideInstallationModel() {
        this.setState({
            showInstallationModal: false,
        });
    }

    render() {
        if (!this.state || this.state.isLoading) {
            return (
                <LoadingProgress />
            );
        }

        const { connectorContext } = this.props;
        const { bcApiGateway, spApiGateway } = connectorContext;

        const {
            validationResult,
            showInstallationModal,
        } = this.state;

        const showAccountManagement = (connectorContext && connectorContext.currentUserRole && connectorContext.currentUserRole === UserRole.Author) ||
            validationResult.accountEmptyGroups.length > 0 ||
            false;

        return (
            <Router>
                <div className="App">
                    <div className="container body-content">
                        <div className="row">
                            <div className="col-md-4">
                                <nav className="leftNav">
                                    <RouteButton to="/" label="Home" activeOnlyWhenExact={true} />
                                    {showAccountManagement &&
                                        <RouteButton to="/account-management" label="Account Management" />
                                    }
                                    <RouteButton to="/add-videos" label="Add Videos" />
                                    <RouteButton to="/manage-videos" label="Manage Videos" />
                                    <RouteButton to="/manage-playlists" label="Manage Playlists" />
                                    <RouteButton to="/manage-experiences" label="Manage Experiences" />
                                </nav>
                            </div>
                            <div className="col-md-8">
                                {this.state &&
                                    <Switch>
                                        <Route path="/" exact={true} component={Home} />
                                        <Route path="/account-management" exact={true}>
                                            <AccountManagement connectorContext={connectorContext} bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} validationResult={validationResult} />
                                        </Route>
                                        <Route
                                            path="/account-management/view/:accountId"
                                            render={props => (
                                                <AccountView connectorContext={connectorContext} accountId={props.match.params.accountId} validationResult={validationResult} />
                                            )}
                                        />
                                        <Route path="/add-videos">
                                            <AddVideos connectorContext={connectorContext} />
                                        </Route>
                                        <Route path="/manage-videos" exact={true}>
                                            <ManageVideos connectorContext={connectorContext} />
                                        </Route>
                                        <Route
                                            path="/manage-videos/edit/:accountId/:videoLocation(SharePoint|Brightcove)/:videoId"
                                            render={props => (
                                                <EditVideo connectorContext={connectorContext} accountId={props.match.params.accountId} videoId={props.match.params.videoId} />
                                            )}
                                        />
                                        <Route path="/manage-playlists" exact={true}>
                                            <ManagePlaylists connectorContext={connectorContext} bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} />
                                        </Route>
                                        <Route
                                            path="/manage-playlists/new/:accountId/playlist"
                                            render={props => (
                                                <EditPlaylist bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} accountId={props.match.params.accountId} />
                                            )}
                                        />
                                        <Route
                                            path="/manage-playlists/edit/:accountId/playlist/:playlistId"
                                            render={props => (
                                                <EditPlaylist bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} accountId={props.match.params.accountId} playlistId={props.match.params.playlistId} />
                                            )}
                                        />
                                        <Route path="/manage-experiences" exact={true}>
                                            <ManageExperiences connectorContext={connectorContext} bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} />
                                        </Route>
                                        <Route
                                            path="/manage-experiences/preview/:accountId/experience/:experienceId"
                                            render={props => (
                                                <PreviewExperience connectorContext={connectorContext} bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} accountId={props.match.params.accountId} experienceId={props.match.params.experienceId} />
                                            )}
                                        />
                                        <Redirect to="/" />
                                    </Switch>
                                }
                            </div>
                        </div>
                        <hr />
                        <footer>
                            <p className="copyright">© 2018 - Brightcove</p>
                            <p className="version">{process.env.REACT_APP_VERSION}</p>
                        </footer>
                    </div>
                    <Modal className="installation-modal" show={showInstallationModal} onHide={this.hideInstallationModel} bsSize="large">
                        <Modal.Header closeButton={true}>
                            <Modal.Title>Brightcove Video Connector for SharePoint Installation</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <InstallationValidation spApiGateway={connectorContext.spApiGateway} validationResult={validationResult} />
                        </Modal.Body>
                        <Modal.Footer />
                    </Modal>
                </div>
            </Router>
        );
    }
}

interface ManageProps {
    connectorContext: ConnectorContext;
    raiseError: (message: string, err?: Error) => boolean;
}

interface ManageState {
    isLoading: boolean;
    validationResult: ConnectorValidationResult;

    showInstallationModal: boolean;
}

export default Manage;



