import { Web, sp } from 'sp-pnp-js';

export class SPGroups {
    private readonly _addinWebUrl?: string;
    private readonly _hostWebUrl: string;
    private readonly _web: Web;

    constructor(hostWebUrl: string, addinWebUrl?: string) {

        this._addinWebUrl = addinWebUrl;
        this._hostWebUrl = hostWebUrl;

        if (!addinWebUrl) {
            this._web = new Web(hostWebUrl);
        } else {
            this._web = sp.crossDomainWeb(addinWebUrl, hostWebUrl);
        }
    }

    /**
     * Gets a value that indicates if the specified group exists in the specified web.
     * @param webUrl 
     * @param groupName 
     */
    public async ensureGroup(groupName: string): Promise<boolean> {
        const groups = await this._web.siteGroups.filter(`Title eq '${groupName}'`).get();

        if (groups.length > 0) {
            return true;
        }

        return false;
    }
    /**
     * Gets all groups defined in the host web.
     */
    public async getSiteGroups(): Promise<Array<SPGroup>> {
        let groups = await this._web.siteGroups.get();
        return this.mapGroups(groups);
    }

    /**
     * Gets all groups that the current user is a member of.
     */
    public async getUserGroups(userEmail?: string): Promise<Array<SPGroup>> {
        let currentUser: any;
        if (!userEmail) {
            currentUser = await this._web.currentUser.select('Id').get();
        } else {
            currentUser = await this._web.siteUsers.getByEmail(userEmail).select('Id').get();
        }

        if (!currentUser) {
            throw new Error('Unable to obtain the current user.');
        }
        
        const userGroups = await this._web.siteUsers.getById(currentUser.Id).groups.get();
        return this.mapGroups(userGroups);
    }

    private mapGroups(groups: any): Array<SPGroup> {
        let result: Array<SPGroup> = [];
        for (let group of groups) {
            result.push({
                id: group.Id,
                title: group.Title
            });
        }

        return result;
    }
}

export interface SPGroup {
    id: number;
    title: string;
}


