import * as React from 'react';
import autobind from 'autobind-decorator';
import * as URI from 'urijs';

export class BrightcovePlayer extends React.Component<BrightcovePlayerProps> {

    @autobind
    getVideoProps(): any {
        const { accountId, mode, modeId, playerWidth, playerHeight, autoplay, muted } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        let videoProps: any = {};

        switch (mode) {
            case 'Playlist':
                videoProps['data-playlist-id'] = modeId;
                break;
            case 'Video':
                videoProps['data-video-id'] = modeId;
                break;
            default:
                throw new Error(`Unsupported mode: ${mode}`);
        }

        videoProps = {
            ...videoProps,
            'data-account': accountId,
            'data-player': playerId,
            'data-embed': 'default',
            'data-usage': `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:javascript`,
            className: 'video-js',
            controls: true,
            autoPlay: autoplay,
            muted: muted,
            style: {
                width: playerWidth ? `${playerWidth}${units}` : undefined,
                height: playerHeight ? `${playerHeight}${units}` : undefined,
                position: 'relative',
                top: '0px', bottom: '0px',
                right: '0px', left: '0px',
            }
        };

        switch (mode) {
            case 'Playlist':
                break;
            case 'Video':
                videoProps.style.position = 'absolute';
                break;
            default:
                throw new Error(`Unsupported mode: ${mode}`);
        }

        return videoProps;
    }

    renderIFrameFixed() {
        const { accountId, mode, modeId, playerWidth, playerHeight, autoplay, muted } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        if (!playerWidth || !playerHeight) {
            return (
                <>Please enter correct values for width/height</>
            );
        }

        const iFrameUri = new URI(`${window.location.protocol}//players.brightcove.net/${accountId}/${playerId}_default/index.html`);
        switch (mode) {
            case 'Video':
                iFrameUri.addQuery('videoId', modeId);
                break;
            case 'Playlist':
                iFrameUri.addQuery('playlistId', modeId);
                break;
            default:
                throw new Error('Unknown Video Mode');
        }

        iFrameUri.addQuery('usage', `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:iframe`);

        if (autoplay) {
            iFrameUri.addQuery('autoplay');
        }

        if (muted) {
            iFrameUri.addQuery('muted');
        }

        const iframeUrl = iFrameUri.toString();

        const outerStyle: React.CSSProperties = {
            display: 'block',
            position: 'relative',
        };

        if (playerWidth) {
            outerStyle.width = `${playerWidth}${units}`;
        }

        if (playerHeight) {
            outerStyle.height = `${playerHeight}${units}`;
        }

        const iframeStyle: React.CSSProperties = {
            width: '100%',
            height: '100%',
            position: 'absolute',
            top: '0px',
            bottom: '0px',
            right: '0px',
            left: '0px',
        };

        return (
            <div style={outerStyle}>
                <iframe
                    src={iframeUrl}
                    style={iframeStyle}
                    allowFullScreen={true}
                    // @ts-ignore
                    webkitallowfullscreen=""
                    // @ts-ignore
                    mozallowfullscreen=""
                />
            </div>
        );
    }

    renderJavaScriptFixed() {
        const { accountId, mode, playerWidth, playerHeight, playerNumThumbnails } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        if (!playerWidth || !playerHeight) {
            return (
                <>Please enter correct values for width/height</>
            );
        }

        const playlistItemWidth = Math.round((parseFloat(playerWidth) - (playerNumThumbnails || 1) * 4) / (playerNumThumbnails || 1));
        const playlistItemHeight = Math.round(playlistItemWidth * parseFloat(playerHeight || '0') / parseFloat(playerWidth || '0'));

        let playerPaddingTop: number | undefined = undefined;
        if (playerHeight && playerWidth) {
            playerPaddingTop = parseFloat(playerHeight) / parseFloat(playerWidth) * 100;
        }

        const scriptUrl = `//players.brightcove.net/${accountId}/${playerId}_default/index.min.js`;
        const videoProps = this.getVideoProps();

        const styleTag = (
            <style type="text/css"> {`
                .vjs-playlist {
                        width: ${playerWidth}${units};
                        height: calc(${playlistItemHeight}${units} + 16px);
                        text-align: center;
                        overflow-x: scroll;
                        overflow-y: hidden;
                        white-space: nowrap;
                        margin: 0;
                        padding: 0;
                  }
                .vjs-playlist-title-container {
                        color: #FFFFFF;
                        /*display: none;*/
                        opacity: 1;
                        font-size: 0.7em;
                        font-family: sans-serif;
                        font-weight: bold;
                }
                .vjs-playlist-now-playing-text {
                        color: #FFFFFF;
                        /*display: none;*
                        opacity: 1;
                        font-size: 0.7em;
                        font-family: sans-serif;
                        font-weight: bold;
                }
                .vjs-up-next-text {
                        color: #FFFFFF;
                        /*display: none;*/
                        opacity: 1;
                        font-family: sans-serif;
                        font-weight: bold;
                        text-align: right;
                }
                .vjs-playlist-duration {
                        color: #FFFFFF;
                        /*display: none;*/
                        opacity: 1;
                        font-family: sans-serif;
                        font-weight: bold;
                }
                .vjs-mouse.vjs-playlist {
                        background-color: #000000;
                }
                li.vjs-playlist-item {
                        background-color: #000000;
                        height: ${playlistItemHeight}${units};
                        width: ${playlistItemWidth}${units};
                        display: inline-block;
                        border: 2px solid #000000;
                        padding: 0;
                        margin: 0;
                        cursor: pointer;
                        vertical-align: middle;
                }
                li.vjs-playlist-item:hover {
                        border-color: #FFFFFF;
                }`}
            </style>
        );

        const outerStyle: React.CSSProperties = {
            display: 'block',
            position: 'relative',
            width: `${playerWidth}${units}`,
        };

        if (mode === 'Playlist') {
            outerStyle.height = `calc(${playerHeight}${units} + ${playlistItemHeight}${units} + 16px)`;
        } else {
            outerStyle.height = `${playerHeight}${units}`;
        }

        return (
            <>
                {mode === 'Playlist' &&
                    styleTag
                }
                <div style={outerStyle}>
                    {mode === 'Playlist' &&
                        <>
                            <video {...videoProps} />
                            <script src={scriptUrl} />
                            <ol className="vjs-playlist vjs-csspointerevents vjs-mouse" />
                        </>
                    }
                    {mode === 'Video' &&
                        <div style={{ paddingTop: playerPaddingTop ? `${playerPaddingTop}%` : undefined }}>
                            <video {...videoProps} />
                            <script src={scriptUrl} />
                        </div>
                    }
                </div>
            </>
        );
    }

    renderJavaScriptResponsive() {
        const { accountId, playerWidth, playerHeight } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        let playerPaddingTop: number | undefined;

        if (!playerWidth || !playerHeight) {
            return (
                <>Please enter correct values for width/height</>
            );
        }

        if (playerHeight && playerWidth) {
            playerPaddingTop = parseFloat(playerHeight) / parseFloat(playerWidth) * 100;
        }

        const scriptUrl = `//players.brightcove.net/${accountId}/${playerId}_default/index.min.js`;
        const videoProps = this.getVideoProps();
        videoProps.style.width = '100%';
        videoProps.style.height = '100%';

        return (
            <div style={{ display: 'block', position: 'relative', minWidth: '0px', maxWidth: `${playerWidth}${units}` }}>
                <div style={{ paddingTop: playerPaddingTop ? `${playerPaddingTop}%` : undefined }}>
                    <video {...videoProps} />
                    <script src={scriptUrl} />
                </div>
            </div>
        );
    }

    render() {
        const { embedType, sizing } = this.props;

        switch (embedType) {
            case 'Standard':
                return this.renderIFrameFixed();
            case 'Advanced':
                switch (sizing) {
                    case 'Fixed':
                        return this.renderJavaScriptFixed();
                    case 'Responsive':
                        return this.renderJavaScriptResponsive();
                    default:
                        throw new Error('Unknown or unsupported embed sizing.');
                }
            default:
                throw new Error('Unknown or supported embed type.');
        }
    }
}

export enum BrightcovePlayerAspectRatio {
    Widescreen = '16:9',
    Standard = '4:3',
    Custom = 'Custom'
}

export function getDefaultBrightcovePlayerProps(): Partial<BrightcovePlayerProps> {
    return {
        connectorVersion: process.env.REACT_APP_VERSION,
        autoplay: false,
        muted: false,
        embedType: 'Advanced',
        sizing: 'Responsive',
        playerNumThumbnails: 5,
        aspectRatio: BrightcovePlayerAspectRatio.Widescreen,
        playerWidth: '640',
        playerHeight: '360',
        units: 'px'
    };
}

export function getBrightcovePlayerPropsFromQueryString(): Partial<BrightcovePlayerProps> {
    const uri = URI();
    const info = uri.query(true) as { [key: string]: string };

    const result = getDefaultBrightcovePlayerProps();

    result.accountId = info.accountId;
    switch (info.playerMode) {
        case 'Video':
            result.mode = 'Video';
            break;
        case 'Playlist':
            result.mode = 'Playlist';
            break;
        default:
            result.mode = 'Video';
            break;
    }

    result.playerId = info.playerId;
    result.modeId = info.modeId;
    result.sharepointVersion = info.SPProductNumber;
    result.autoplay = info.autoplay === 'true' ? true : false;
    result.muted = info.muted === 'true' ? true : false;

    return result;
}

export interface BrightcovePlayerProps {
    accountId: string;
    sharepointVersion: string;
    connectorVersion: string;
    mode: 'Video' | 'Playlist';
    modeId: string;
    playerId?: string;
    autoplay: boolean;
    muted: boolean;
    embedType: 'Standard' | 'Advanced';
    sizing: 'Responsive' | 'Fixed';
    aspectRatio: BrightcovePlayerAspectRatio;
    playerWidth?: string;
    playerHeight?: string;
    playerNumThumbnails?: number;
    units?: 'px' | 'em';
}


