import { SPList } from './SPList';
import { SPListDefinition } from './SPListDefinition';

export interface BrightcoveSettings {
    proxyName: string;
    proxyUrl: string;
}

export const BrightcoveSettingsListDefinition: SPListDefinition = {
    listName: 'BrightcoveSettings',
    itemName: 'BrightcoveSettings',
    itemNamePlural: 'BrightcoveSettings',
    fieldMap: {
        Id: { path: 'id', type: 'text', create: false },
        GUID: { path: 'guid', type: 'text', create: false },
        Title: { path: 'proxyName', type: 'text', create: false },
        ProxyUrl: { path: 'proxyUrl', type: 'text', create: true },
    }
};

export class BrightcoveSettingsList extends SPList<BrightcoveSettings> {

    constructor(hostWebUrl: string, addInWebUrl?: string, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
        super(BrightcoveSettingsListDefinition, hostWebUrl, addInWebUrl, errorCallback);
    }

    /**
     * Returns a value that indicates if the brightcove proxy settings have been defined.
     */
    public async ensureProxySettings(): Promise<boolean> {
        const brightcoveSettings = await this.getItemsByFieldValue('Title', 'Proxy');
        if (brightcoveSettings.length === 1 && brightcoveSettings[0].proxyUrl) {
            return true;
        }

        return false;
    }

    public async getBrightcoveSettings(): Promise<BrightcoveSettings> {
        const brightcoveSettings = await this.getItemsByFieldValue('Title', 'Proxy');

        if (brightcoveSettings.length > 1) {
            throw new Error(`There are multiple Brightcove settings list items titled 'Proxy'.`);
        } else if (brightcoveSettings.length < 1) {
            throw new Error(`A Brightcove setting list item titled 'Proxy' could not be found in the Brightcove Settings List.`);
        }

        return brightcoveSettings[0];
    }
}


