import { SPList } from './SPList';
import { SPListDefinition } from './SPListDefinition';

export interface Video {
    id: string;
    guid: string;
    title: string;
    creationDate: string;
    shortDescription: string;
    longDescription: string;
    active: boolean;
    relatedLink: string;
    relatedLinkText: string;
    videoImage: { Description: string, Url: string };
    referenceId: string;
    startDate: string;
    endDate: string;
    economic: string;
    videoThumbnail: { Description: string, Url: string };
    accountName: string;
    keywords: string;
}

export const VideosListDefinition: SPListDefinition = {
    listName: 'Videos',
    itemName: 'Video',
    itemNamePlural: 'Videos',
    fieldMap: {
        Id: { path: 'id', type: 'text', create: false },
        GUID: { path: 'guid', type: 'text', create: false },
        Title: { path: 'title', type: 'text', create: false },
        Created: { path: 'creationDate', type: 'date', create: false },
        ShortDescription: { path: 'shortDescription', type: 'text', create: true },
        LongDescription: { path: 'longDescription', type: 'text', create: true },
        Active: { path: 'active', type: 'boolean', create: true },
        RelatedLink: { path: 'relatedLink', type: 'link', create: true },
        RelatedLinkText: { path: 'relatedLinkText', type: 'text', create: true },
        VideoImage: { path: 'videoImage', type: 'link', create: true },
        ReferenceID: { path: 'referenceId', type: 'text', create: true },
        StartDate: { path: 'startDate', type: 'datetime', create: true },
        EndDate: { path: 'endDate', type: 'datetime', create: true },
        Economic: { path: 'economic', type: 'choice', create: true, choices: ['Ad Supported', 'Bronze', 'Silver', 'Gold'] },
        VideoThumbnail: { path: 'videoThumbnail', type: 'link', create: true },
        Account: { path: 'accountName', type: 'text', create: true },
        Keywords: { path: 'keywords', type: 'multiline-text', create: true },
    }
};

export class VideosList extends SPList<Video> {
    constructor(hostWebUrl: string, addInWebUrl?: string, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
        super(VideosListDefinition, hostWebUrl, addInWebUrl, errorCallback);
    }

    public async getVideosByAccountName(accountName: string): Promise<Array<Video>> {
        if (!accountName) {
            throw new Error('accountName must be specified.');
        }

        const videos = await this.getItemsByFieldValue('Account', accountName);

        return videos;
    }

    public async getVideoByVideoId(videoId: string): Promise<Video | undefined> {
        if (!videoId) {
            throw new Error('videoId must be specified.');
        }

        const videos = await this.getItemsByFieldValue('ReferenceID', videoId);

        if (videos.length > 1) {
            throw new Error(`There are multiple video entries with the id '${videoId}'`);
        } else if (videos.length < 1) {
            return;
        }

        return videos[0];
    }

}


