import * as React from 'react';
import { observer } from 'mobx-react';
import { Button } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow, Options } from 'react-bootstrap-table';
import autobind from 'autobind-decorator';
import { AccountPicker, Spinner } from '../common';
import { ExperienceEmbed } from '../embeds';

import { Account, SharePointPageType, SPUtility } from '../../services/sharepoint';
import { BCExperience } from '@brightcove/video-cloud-sdk';

import * as URI from 'urijs';
import { filter } from 'lodash';
import { ConnectorContext } from '../../services/connector';

@observer
export class MiniExperiences extends React.Component<MiniExperiencesProps, MiniExperiencesState> {
    constructor(props: MiniExperiencesProps, context?: any) {
        super(props, context);

        this.state = {
            experiencesFilter: '',
            isLoadingExperiences: true,
            allExperiences: [],
            filteredExperiences: [],
            showAddWebPartButton: false
        };
    }

    async componentDidMount() {
        this.loadExperiences();

        const spApiGateway = this.props.connectorContext.spApiGateway;
        const webPartPageUrl = this.props.connectorContext.spAddinContext.Referrer;

        const pageType = await spApiGateway.spEmbedUtil.getWebPartPageType(webPartPageUrl);

        // TODO: This is test code
        // await spApiGateway.spEmbedUtil.insertScriptEditorWebPart(webPartPageUrl);

        this.setState({
            showAddWebPartButton: pageType !== SharePointPageType.Other
        });
    }

    async loadExperiences() {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        if (!selectedAccount) {
            return;
        }

        this.setState({
            isLoadingExperiences: true,
            experiencesFilter: ''
        });

        const experiencesApi = connectorContext.bcApiGateway.experiencesApi;
        const experiencesResponse = await experiencesApi.getExperiences(selectedAccount.accountId);

        this.setState({
            isLoadingExperiences: false,
            allExperiences: experiencesResponse.items,
            filteredExperiences: experiencesResponse.items
        });
    }

    @autobind
    async handleAccountChange(newAccount: Account) {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        if (!selectedAccount || selectedAccount.accountId !== newAccount.accountId) {
            connectorContext.selectedAccount = newAccount;
            this.loadExperiences();
        }
    }

    @autobind
    handleExperiencesFilterChange(experiencesFilter: string) {
        this.setState({
            experiencesFilter
        });

        const { allExperiences } = this.state;
        let filteredExperiences = allExperiences;
        if (experiencesFilter) {
            const strFilter = experiencesFilter.toLowerCase();
            filteredExperiences = filter(filteredExperiences, (ex) => ex.name && ex.name.toLowerCase().indexOf(strFilter) > -1);
        }

        this.setState({
            filteredExperiences
        });
    }

    @autobind
    experienceSelected(experience: BCExperience, isSelected: boolean, e: any) {
        let selectedExperience: BCExperience | undefined;
        if (isSelected) {
            selectedExperience = experience;
        }
        this.setState({
            selectedExperience
        });
    }

    @autobind
    renderExperienceNameCell(cell: any, experience: BCExperience, extraData: { selectedAccount: Account }) {
        let uri = URI(`${window.location.origin}${window.location.pathname}${window.location.search}`);
        uri = uri.removeQuery('mini');
        uri = uri.removeQuery('IsDlg');

        const experienceUrl = `${uri.toString()}#/manage-experiences/preview/${extraData.selectedAccount.accountId}/experience/${experience.id}`;

        return (
            <>
                <a href={experienceUrl} target="_blank">{experience.name}</a>
                <br />
                <div className="small">{experience.template}</div>
                <div className="small">{experience.id}</div>
            </>
        );
    }

    render() {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        const {
            experiencesFilter,
            isLoadingExperiences,
            filteredExperiences,
            selectedExperience
        } = this.state;

        const labelClass = 'col-xs-3 control-label small';
        const controlClass = 'col-xs-9 word-wrap small';

        const selectRow: SelectRow = {
            mode: 'radio',
            clickToSelect: true,
            className: 'info',
            onSelect: this.experienceSelected
        };

        const experienceTableOptions: Options = {
            defaultSortName: 'updatedAt',  // default sort column name
            defaultSortOrder: 'desc'  // default sort order
        };

        return (
            <div className="form-horizontal">
                <div className="form-group">
                    <label className={labelClass}>Brightcove Account:</label>
                    <div className={controlClass}>
                        <AccountPicker className="input-sm" availableAccounts={connectorContext.availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Experiences Filter:</label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Search"
                                className="form-control input-sm"
                                value={experiencesFilter}
                                onChange={(e) => this.handleExperiencesFilterChange(e.target.value)}
                            />
                            <div className="input-group-btn searchButton">
                                <Button bsSize="small"><i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                </div>
                {isLoadingExperiences && selectedAccount &&
                    <Spinner loadingText="Loading Experiences..." />
                }
                {!isLoadingExperiences && selectedAccount &&
                    <>
                        <div className="well" style={{ height: '250px', overflow: 'auto' }} >
                            <BootstrapTable data={filteredExperiences} striped={true} bordered={false} selectRow={selectRow} options={experienceTableOptions}>
                                <TableHeaderColumn dataField="name" dataFormat={this.renderExperienceNameCell} formatExtraData={{ selectedAccount }} dataSort={true}>Name</TableHeaderColumn>
                                <TableHeaderColumn dataField="description" dataSort={true} hidden={true}>Short Description</TableHeaderColumn>
                                <TableHeaderColumn dataField="updatedAt" dataSort={true} hidden={true}>Updated At</TableHeaderColumn>
                                <TableHeaderColumn dataField="id" dataSort={true} isKey={true} hidden={true}>ID</TableHeaderColumn>
                            </BootstrapTable>
                            <hr />
                        </div>
                        <ExperienceEmbed
                            layoutMode="Mini"
                            connectorContext={connectorContext}
                            experience={selectedExperience}
                            labelClass={labelClass}
                            controlClass={controlClass}
                        />
                        <div className="col-xs-12 small" style={{ paddingTop: '5px' }}>
                            <Button className="pull-right" bsSize="small" onClick={() => SPUtility.closeParentDialog(false)}>Close Dialog</Button>
                        </div>
                    </>
                }
            </div>
        );
    }
}

interface MiniExperiencesProps {
    connectorContext: ConnectorContext;
}

interface MiniExperiencesState {
    selectedExperience?: BCExperience;
    experiencesFilter: string;
    isLoadingExperiences: boolean;
    allExperiences: Array<BCExperience>;
    filteredExperiences: Array<BCExperience>;
    showAddWebPartButton: boolean;
}


