import * as React from 'react';
import { observer } from 'mobx-react';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { AccountPicker } from '../common';
import { PlaylistEmbed } from '../embeds';
import { Spinner } from '../common';
import autobind from 'autobind-decorator';
import { PlaylistSearchControls } from '.';

import { BrightcoveApiGateway, BCPlaylist, BCPlaylistType } from '@brightcove/video-cloud-sdk';
import { SharePointApiGateway, Account } from '../../services/sharepoint';
import { ConnectorContext, PlaylistSearchParameters, getDefaultPlaylistSearchParameters } from '../../services/connector';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IconName } from '@fortawesome/fontawesome';

import './ManagePlaylists.css';

@observer
export class ManagePlaylists extends React.Component<ManagePlaylistProps, ManagePlaylistState> {
    constructor(props: ManagePlaylistProps, context?: any) {
        super(props, context);

        this.state = {
            isAddingPlaylist: false,
            isLoadingPlaylists: true,
            showEmbedModal: false,
            searchParameters: getDefaultPlaylistSearchParameters()
        };
    }

    componentDidMount() {
        this.searchForPlaylists(this.state.searchParameters);
    }

    @autobind
    async searchForPlaylists(searchParams: Partial<PlaylistSearchParameters>): Promise<void> {
        const { selectedAccount } = this.props.connectorContext;
        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingPlaylists: true
        });

        const { connectorContext } = this.props;
        const { bcApiGateway } = this.props.connectorContext;
        const playlistsApi = bcApiGateway.playlistsApi;

        const bcParams = connectorContext.getBCPlaylistSearchParams(searchParams);
        const playlists = await playlistsApi.getPlaylists(selectedAccount.accountId, bcParams);

        this.setState({
            isLoadingPlaylists: false,
            playlists
        });
    }

    @autobind
    async handleAccountChange(newAccount: Account) {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        if (!selectedAccount || selectedAccount.accountId !== newAccount.accountId) {
            connectorContext.selectedAccount = newAccount;
            this.searchForPlaylists(this.state.searchParameters);
        }
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<PlaylistSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForPlaylists(searchParameters);
        }
    }

    @autobind
    showEmbedModal(playlistId: string) {
        this.setState({
            showEmbedModal: true,
            embedPlaylistId: playlistId
        });
    }

    @autobind
    hideEmbedModal() {
        this.setState({
            showEmbedModal: false
        });
    }

    @autobind
    renderPlaylistNameCell(cell: any, playlist: BCPlaylist, extraData: { selectedAccount: Account }): React.ReactElement<any> {
        const { selectedAccount } = extraData;
        const editPlaylistUrl = `#/manage-playlists/edit/${selectedAccount && selectedAccount.accountId}/playlist/${playlist.id}`;
        return (
            <>
                <a href={editPlaylistUrl}>{playlist.name}</a>
                <br />
                {playlist.video_ids && playlist.video_ids.length > 0 &&
                    <>
                        <div style={{ fontSize: 'small' }}>Video IDs in list: </div>
                        <div style={{ fontSize: 'xx-small' }}>
                            {playlist.video_ids.map((videoId) =>
                                <span key={videoId} style={{ paddingLeft: '5px', float: 'left' }}>{videoId}</span>
                            )}
                        </div>
                    </>
                }
                {(!playlist.video_ids || playlist.video_ids.length === 0) && playlist.type === BCPlaylistType.Manual &&
                    <i>This playlist does not contain any videos.</i>
                }
                {(!playlist.video_ids || playlist.video_ids.length === 0) && playlist.type !== BCPlaylistType.Manual &&
                    <i>This playlist is a Smart Playlist</i>
                }
            </>
        );
    }

    @autobind
    renderEditLink(cell: any, playlist: BCPlaylist, extraData: { selectedAccount: Account }): React.ReactElement<any> {
        const { selectedAccount } = extraData;
        const editPlaylistUrl = `#/manage-playlists/edit/${selectedAccount && selectedAccount.accountId}/playlist/${playlist.id}`;

        return (
            <div className="result text-center">
                <div>
                    <Button
                        bsStyle="link"
                        style={{ width: '70px' }}
                        href={editPlaylistUrl}
                    >
                        Edit
                    </Button>
                </div>
                <div>
                    <Button
                        bsStyle="success"
                        style={{ width: '70px' }}
                        bsSize="small"
                        onClick={() => this.showEmbedModal(playlist.id)}
                    >
                        Embed
                    </Button>
                </div>
            </div>
        );
    }

    render() {
        const plusIconName: IconName = 'plus-circle';

        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        const {
            isAddingPlaylist,
            isLoadingPlaylists,
            playlists,
            showEmbedModal,
            embedPlaylistId,
            searchParameters,
        } = this.state;

        const labelClass = 'col-md-3 control-label';
        const controlClass = 'col-md-9';

        if (isAddingPlaylist) {
            return (
                <Spinner loadingText="Creating New Playlist..." />
            );
        }

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li className="active">Manage Playlists</li>
                </ol>
                <div className="search form-horizontal">
                    <div className="form-group">
                        <label className={labelClass}>Select Account:</label>{' '}
                        <div className={controlClass}>
                            <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccount={connectorContext.selectedAccount} onChange={this.handleAccountChange} />
                        </div>
                    </div>
                </div>
                <div className="search form-horizontal">
                    {selectedAccount &&
                        <>
                            <PlaylistSearchControls
                                labelClass={labelClass}
                                controlClass={controlClass}
                                searchParameters={searchParameters}
                                onSearchParametersChanged={this.handleSearchParametersChanged}
                                onSearchForPlaylists={this.searchForPlaylists}
                            />
                            {isLoadingPlaylists &&
                                <Spinner loadingText="Loading Playlists..." />
                            }
                            {!isLoadingPlaylists && playlists &&
                                <>
                                    <BootstrapTable data={playlists} striped={true} bordered={false}>
                                        <TableHeaderColumn dataField="name" dataFormat={this.renderPlaylistNameCell} formatExtraData={{ selectedAccount }} dataSort={true}>Name</TableHeaderColumn>
                                        <TableHeaderColumn dataField="description" dataSort={true}>Short Description</TableHeaderColumn>
                                        <TableHeaderColumn dataField="type" dataSort={true} dataFormat={(cell, row) => { return cell === BCPlaylistType.Manual ? 'Manual Playlist' : 'Smart Playlist'; }}>Type</TableHeaderColumn>
                                        <TableHeaderColumn dataField="id" dataSort={true} isKey={true}>ID</TableHeaderColumn>
                                        <TableHeaderColumn width="90" dataFormat={this.renderEditLink} formatExtraData={{ selectedAccount }}>Edit Link</TableHeaderColumn>
                                    </BootstrapTable>
                                    <hr />
                                    <Button bsSize="large" bsStyle="default" href={`#/manage-playlists/new/${selectedAccount && selectedAccount.accountId}/playlist`}>
                                        <FontAwesomeIcon icon={plusIconName} /> Add New Playlist
                            </Button>
                                    <hr />
                                </>
                            }
                            {embedPlaylistId &&
                                <Modal className="embed" show={showEmbedModal} onHide={this.hideEmbedModal} bsSize="large">
                                    <Modal.Header closeButton={true}>
                                        <Modal.Title>Embed Playlist</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <PlaylistEmbed
                                            connectorContext={connectorContext}
                                            account={selectedAccount}
                                            playlistId={embedPlaylistId}
                                        />
                                    </Modal.Body>
                                    <Modal.Footer />
                                </Modal>
                            }
                        </>
                    }
                </div>
            </>
        );
    }
}

interface ManagePlaylistProps {
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spApiGateway: SharePointApiGateway;
}

interface ManagePlaylistState {
    isAddingPlaylist: boolean;
    isLoadingPlaylists: boolean;
    searchParameters: Partial<PlaylistSearchParameters>;
    playlists?: Array<BCPlaylist>;

    showEmbedModal: boolean;
    embedPlaylistId?: string;
}



