import { Video as SPVideo } from '../../services/sharepoint/Videos';
import { BCVideo, BCVideoTextTrack } from '@brightcove/video-cloud-sdk';
import { filter, startsWith } from 'lodash';

/**
 * Represents a Video suitable for the app.
 */
export interface ConnectorVideo {
    videoId: string;
    referenceId: string;
    name: string;
    shortDescription: string;
    longDescription: string;
    complete: boolean;
    state: string;
    relatedLinkUrl: string;
    relatedLinkText: string;
    creationDate: Date;
    startAvailabilityDate?: Date;
    endAvailabilityDate?: Date;
    economics: string;
    tags: Array<string>;
    customFields: { [fieldName: string]: string };
    textTracks?: Array<Partial<BCVideoTextTrack>>;
    videoStillUrl: string;
    thumbnailUrl: string;
}

export function mapBCVideoToMVideo(bcVideo: BCVideo): ConnectorVideo {

    const video: ConnectorVideo = {
        videoId: bcVideo.id,
        referenceId: bcVideo.reference_id || '',
        name: bcVideo.name,
        shortDescription: bcVideo.description || '',
        longDescription: bcVideo.long_description || '',
        complete: bcVideo.complete || false,
        state: bcVideo.state || '',
        relatedLinkUrl: bcVideo.link ? bcVideo.link.url : '',
        relatedLinkText: bcVideo.link ? bcVideo.link.text : '',
        creationDate: new Date(bcVideo.created_at),
        startAvailabilityDate: bcVideo.schedule && bcVideo.schedule.starts_at ? new Date(bcVideo.schedule.starts_at) : undefined,
        endAvailabilityDate: bcVideo.schedule && bcVideo.schedule.ends_at ? new Date(bcVideo.schedule.ends_at) : undefined,
        economics: bcVideo.economics || '',
        tags: bcVideo.tags ? filter(bcVideo.tags, (tag) => !startsWith(tag, 'bcsp-')) : [],
        customFields: bcVideo.custom_fields || [],
        textTracks: bcVideo.text_tracks || [],
        videoStillUrl: '',
        thumbnailUrl: ''
    };

    // The images might not be available if the video is still processing.
    if (bcVideo.images) {

        if (bcVideo.images.poster) {
            video.videoStillUrl = bcVideo.images.poster.src || '';
        }

        if (bcVideo.images.thumbnail) {
            video.thumbnailUrl = bcVideo.images.thumbnail.src || '';
        }
    }

    return video;
}

export function mapMVideoToBCVideo(video: ConnectorVideo, bcspTags?: Array<string>): Partial<BCVideo> {
    // Map the MVideo back to a partial bc video object.
    let bcVideo: Partial<BCVideo> = {
        name: video.name,
        description: video.shortDescription,
        long_description: video.longDescription,
        state: video.state,
        link: {
            text: video.relatedLinkText,
            url: video.relatedLinkUrl
        },
        schedule: {
            starts_at: video.startAvailabilityDate ? video.startAvailabilityDate.toISOString() : null,
            ends_at: video.endAvailabilityDate ? video.endAvailabilityDate.toISOString() : null,
        },
        economics: video.economics,
        tags: video.tags,
        custom_fields: video.customFields
    };

    if (bcspTags) {
        if (!bcVideo.tags) {
            bcVideo.tags = [];
        }

        for (const bcspTag of bcspTags) {
            bcVideo.tags.push(bcspTag);
        }
    }

    // For the text tracks that have an id, go ahead and patch them.
    bcVideo.text_tracks = filter(video.textTracks, (tt) => !!tt.id) as Array<BCVideoTextTrack>;

    return bcVideo;
}

export function mapSPVideoToMVideo(spVideo: SPVideo): ConnectorVideo {
    let video: ConnectorVideo = {
        name: spVideo.title,
        videoId: spVideo.referenceId, // FIXME: This doesn't seem right

        referenceId: spVideo.referenceId,
        shortDescription: spVideo.shortDescription,
        longDescription: spVideo.longDescription,
        complete: true,
        state: spVideo.active ? 'ACTIVE' : 'INACTIVE',
        economics: spVideo.economic,

        relatedLinkText: spVideo.relatedLinkText,
        relatedLinkUrl: spVideo.relatedLink,

        creationDate: new Date(spVideo.creationDate),
        startAvailabilityDate: new Date(spVideo.startDate),
        endAvailabilityDate: spVideo.endDate ? new Date(spVideo.endDate) : undefined,

        tags: spVideo.keywords ? filter(spVideo.keywords.split(','), (tag) => !startsWith(tag, 'bcsp-')) : [],

        videoStillUrl: spVideo.videoImage ? spVideo.videoImage.Url : '',
        thumbnailUrl: spVideo.videoThumbnail ? spVideo.videoThumbnail.Url : '',
        customFields: {} // TODO: Get the sharepoint fields starting with c1 and get their values.
    };

    return video;
}

export function mapMVideoToSPVideo(accountName: string, video: ConnectorVideo): Partial<SPVideo> {
    let spVideo: Partial<SPVideo> = {
        title: video.name,
        referenceId: video.videoId,

        shortDescription: video.shortDescription,
        longDescription: video.longDescription,
        active: video.state === 'ACTIVE' ? true : false,
        economic: video.economics,

        relatedLinkText: video.relatedLinkText,
        relatedLink: video.relatedLinkUrl,

        // creationdate
        startDate: video.startAvailabilityDate ? video.startAvailabilityDate.toISOString() : '',
        endDate: video.endAvailabilityDate ? video.endAvailabilityDate.toISOString() : '',

        keywords: video.tags.join(','),

        videoImage: {
            Description: video.videoStillUrl,
            Url: video.videoStillUrl
        },
        videoThumbnail: {
            Description: video.thumbnailUrl,
            Url: video.thumbnailUrl,
        },

        accountName: accountName
    };

    return spVideo;
}


