import * as React from 'react';
import { observer } from 'mobx-react';
import Dropzone from 'react-dropzone';
import { ImageFile } from 'react-dropzone';
import { Prompt } from 'react-router';
import { Button, ProgressBar } from 'react-bootstrap';
import { AccountPicker, FolderPicker } from '../common';

import { BCIngestProfile, BCFolder } from '@brightcove/video-cloud-sdk';
import { Account } from '../../services/sharepoint';
import { ConnectorContext, ConnectorVideoUpload } from '../../services/connector';

import { filter, cloneDeep } from 'lodash';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IconName } from '@fortawesome/fontawesome';
import autobind from 'autobind-decorator';
import './AddVideos.css';

@observer
export class AddVideos extends React.Component<AddVideosProps, AddVideosState> {
    constructor(props: AddVideosProps, context?: any) {
        super(props, context);

        this.state = {
            videoFiles: [],
            currentTags: []
        };
    }

    @autobind
    onDrop(acceptedFiles: ImageFile[], rejectedFiles: ImageFile[]) {

        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;
        const { videoFiles, selectedFolder, currentTags } = this.state;

        if (!selectedAccount) {
            throw new Error('An account must be selected.');
        }

        acceptedFiles.forEach(async file => {
            const uploadingVideo: ConnectorVideoUpload = {
                file,
                account: cloneDeep(selectedAccount),
                targetFolder: cloneDeep(selectedFolder),
                tags: cloneDeep(currentTags),
                fileName: file.name,
                isUploading: true,
                isComplete: false,
                hasError: false,
                status: 'Starting Upload...',
                progress: 10,
            };

            videoFiles.push(uploadingVideo);
            connectorContext.performDynamicIngest(uploadingVideo, (video) => {
                const videoIx = videoFiles.indexOf(video);
                videoFiles[videoIx] = video;

                // TODO: Instruct react to only partially update the specific video's state?
                this.setState({
                    videoFiles
                });
            });
        });

        this.setState({
            videoFiles
        });
    }

    updateVideoStatus(video: ConnectorVideoUpload) {
        this.setState({
            videoFiles: [...this.state.videoFiles]
        });
    }

    @autobind
    handleSelectedAccountChange(selectedAccount: Account) {
        const { connectorContext } = this.props;
        connectorContext.selectedAccount = selectedAccount;
        this.setState({
            selectedAccount
        });
    }

    @autobind
    handleSelectedFolderChange(selectedFolder?: BCFolder) {
        this.setState({
            selectedFolder
        });
    }

    @autobind
    handleOnTagsChange(event: React.ChangeEvent<HTMLInputElement>) {

        let currentTags: Array<string> = [];
        const str = event.target.value;
        if (str) {
            currentTags = str.split(',').map((s) => s.trim());
        }

        this.setState({
            currentTags
        });
    }

    render() {

        let dropzoneRef: Dropzone | null;
        const uploadIconName: IconName = 'arrow-circle-up';

        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        const { videoFiles, selectedFolder, currentTags } = this.state;
        const uploadingVideoFiles = filter(videoFiles, { isUploading: true });

        const strCurrentTags = currentTags.join(',');

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li className="active">Add Videos</li>
                </ol>
                <div className="formContent">
                    <label>Select Account:</label>{' '}
                    <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccount={connectorContext.selectedAccount} onChange={this.handleSelectedAccountChange} />
                </div>
                {selectedAccount &&
                    <>
                        <Dropzone className="dropzone" ref={(node) => { dropzoneRef = node; }} onDrop={this.onDrop} disableClick={true} style={{ textAlign: 'center', marginTop: '5px' }}>
                            <div className="drag-videos-icon">
                                <FontAwesomeIcon icon={uploadIconName} style={{ color: '#d32781' }} size="5x" />
                            </div>
                            <h5 className="drag-videos-target">Drag Your Videos Here</h5>
                            <div className="drag-or-label dimmable">-or-</div>
                            <Button bsStyle="primary" onClick={() => { dropzoneRef && dropzoneRef.open(); }}>Browse for Videos</Button>
                        </Dropzone>
                        <div className="form-group" style={{ paddingTop: '5px' }}>
                            <label className="control-label">Folder for all current uploads</label>
                            <div className="control-group">
                                <FolderPicker connectorContext={connectorContext} selectedFolderId={selectedFolder && selectedFolder.id} onChange={this.handleSelectedFolderChange} />
                            </div>
                        </div>
                        <div className="form-group" style={{ paddingTop: '5px' }}>
                            <label className="control-label">Tags for all current uploads (comma-separated)</label>
                            <div className="control-group">
                                <input className="form-control" type="text" value={strCurrentTags} onChange={this.handleOnTagsChange} />
                            </div>
                        </div>
                        <hr />
                        <table className="table table-striped">
                            <tbody>
                                {videoFiles.map((video, ix) =>
                                    <tr key={ix} className="row">
                                        <td>
                                            <div>
                                                <div className="result">{video.fileName}&nbsp;
                                                    <span className="small">
                                                        (Account:&nbsp;<strong>{video.account.title}</strong>&nbsp;
                                                        Folder:&nbsp;<strong>{video.targetFolder && video.targetFolder.name}{!video.targetFolder && <i>Root Folder</i>}</strong>&nbsp;
                                                         Tags:&nbsp;<strong>{video.tags && video.tags.length > 0 && video.tags.join(', ')}{(!video.tags || video.tags.length <= 0) && <i>No Tags Entered</i>}</strong>)
                                                    </span>
                                                </div>
                                                <div>
                                                    <ProgressBar bsStyle={video.hasError ? 'danger' : 'info'} now={video.progress} striped={true} active={!video.isComplete} />
                                                    <div style={{ fontSize: 'small' }}>
                                                        <span>{video.status}</span>
                                                        {video.progress === 100 && !video.hasError && video.bcVideo &&
                                                            <Button bsStyle="link" href={`${window.location.origin}${window.location.pathname}${window.location.search}#/manage-videos/edit/${video.account.accountId}/Brightcove/${video.bcVideo.id}`} target={uploadingVideoFiles.length > 0 ? '_blank' : '_self'}>Edit Video</Button>
                                                        }
                                                    </div>
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                        <Prompt
                            when={uploadingVideoFiles && uploadingVideoFiles.length > 0}
                            message="There are videos currently uploading. Are you sure you wish to leave?"
                        />
                    </>
                }
            </>
        );
    }
}

interface AddVideosProps {
    connectorContext: ConnectorContext;
    onAllowNavigateChange?: (allowNavigate: boolean) => void;
}

interface AddVideosState {
    videoFiles: Array<ConnectorVideoUpload>;
    selectedAccount?: Account;
    selectedIngestProfile?: BCIngestProfile;
    selectedFolder?: BCFolder;
    currentTags: Array<string>;
}



