import * as React from 'react';
import autobind from 'autobind-decorator';

import { BrightcoveExperienceUnits, BrightcoveExperienceOverrideMode } from '.';
import { BCExperience } from '@brightcove/video-cloud-sdk';

import * as URI from 'urijs';

export class BrightcoveExperience extends React.Component<BrightcoveExperienceProps> {

    @autobind
    renderIFrameFixed() {
        const {
            accountId,
            experienceId,
            experience,
            overrideWidth,
            playerWidth,
            overrideHeight,
            playerHeight,
            playerUnits,
            overrideExperienceVideos,
            experienceOverrideMode,
            experienceOverrideModeIds,
        } = this.props;

        const scriptUri = new URI(`https://players.brightcove.net/${accountId}/experience_${experienceId}/index.html`);
        scriptUri.addQuery('usage', `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:experienceiframe`);
        if (overrideExperienceVideos) {
            switch (experienceOverrideMode) {
                case BrightcoveExperienceOverrideMode.Playlist:
                    if (experienceOverrideModeIds) {
                        scriptUri.addQuery('videoIds', '');
                        scriptUri.addQuery('playlistId', experienceOverrideModeIds.join(','));
                    }
                    break;
                case BrightcoveExperienceOverrideMode.Videos:
                    if (experienceOverrideModeIds) {
                        scriptUri.addQuery('videoIds', experienceOverrideModeIds.join(','));
                        scriptUri.addQuery('playlistId', '');
                    }
                    break;
                default:
                    throw new Error('Unknown or unsupported Experience Override Mode');
            }
        }

        const scriptUrl = scriptUri.toString().replace(/%2C/g, ',');

        const style: React.CSSProperties = {
            display: 'block',
            position: 'relative',
        };

        if (experience && experience.width) {
            style.width = `${experience.width}px`;
        }

        if (experience && experience.height) {
            style.height = `${experience.height}px`;
        }

        if (overrideWidth) {
            style.width = `${playerWidth}${playerUnits}`;
        }

        if (overrideHeight) {
            style.height = `${playerHeight}${playerUnits}`;
        }

        return (
            <div style={style}>
                <iframe
                    style={{ width: '100%', height: '100%', top: '0px', bottom: '0px', right: '0px', left: '0px', border: 'none', marginLeft: 'auto', marginRight: 'auto', position: 'absolute' }}
                    src={scriptUrl}
                    allowFullScreen={true}
                    // @ts-ignore
                    webkitallowfullscreen=""
                    // @ts-ignore
                    mozallowfullscreen=""
                />
            </div>
        );
    }

    @autobind
    renderJavaScriptFixed() {
        const {
            accountId,
            experienceId,
            experience,
            overrideWidth,
            playerWidth,
            overrideHeight,
            playerHeight,
            playerUnits,
            overrideExperienceVideos,
            experienceOverrideMode,
            experienceOverrideModeIds
        } = this.props;

        const scriptUrl = `https://players.brightcove.net/${accountId}/experience_${experienceId}/live.js`;

        const style: React.CSSProperties = {
            display: 'block',
            position: 'relative',
        };

        if (experience && experience.width) {
            style.width = `${experience.width}px`;
        }
        if (experience && experience.height) {
            style.height = `${experience.height}px`;
        }

        if (overrideWidth) {
            style.width = `${playerWidth}${playerUnits}`;
        }

        if (overrideHeight) {
            style.height = `${playerHeight}${playerUnits}`;
        }

        const divProps: any = {
            'data-experience': experienceId,
            'data-usage': `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:experiencejavascript`
        };

        if (overrideExperienceVideos) {
            switch (experienceOverrideMode) {
                case BrightcoveExperienceOverrideMode.Playlist:
                    if (experienceOverrideModeIds) {
                        divProps['data-video-ids'] = '';
                        divProps['data-playlist-id'] = experienceOverrideModeIds.join(',');
                    }
                    break;
                case BrightcoveExperienceOverrideMode.Videos:
                    if (experienceOverrideModeIds) {
                        divProps['data-video-ids'] = experienceOverrideModeIds.join(',');
                        divProps['data-playlist-id'] = '';
                    }
                    break;
                default:
                    throw new Error('Unknown or unsupported Experience Override Mode');
            }
        }

        return (
            <>
                <div style={style} {...divProps} />
                <script src={scriptUrl} />
            </>
        );
    }

    @autobind
    renderJavaScriptResponsive() {
        const {
            accountId,
            experienceId,
            experience,
            overrideWidth,
            playerWidth,
            overrideHeight,
            playerHeight,
            playerUnits,
            overrideExperienceVideos,
            experienceOverrideMode,
            experienceOverrideModeIds
        } = this.props;

        const scriptUrl = `https://players.brightcove.net/${accountId}/experience_${experienceId}/live.js`;

        const style: React.CSSProperties = {};
        if (experience && experience.width) {
            style.minWidth = `0`;
            style.maxWidth = `${experience.width}px`;
        }

        if (experience && experience.height) {
            style.minHeight = `0`;
            style.maxHeight = `${experience.height}px`;
        }

        if (overrideWidth) {
            style.minWidth = `0`;
            style.maxWidth = `${playerWidth}${playerUnits}`;
        }

        if (overrideHeight) {
            style.minHeight = `0`;
            style.maxHeight = `${playerHeight}${playerUnits}`;
        }

        const divProps: any = {
            'data-experience': experienceId,
            'data-usage': `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:experiencejavascript`
        };

        if (overrideExperienceVideos) {
            switch (experienceOverrideMode) {
                case BrightcoveExperienceOverrideMode.Playlist:
                    if (experienceOverrideModeIds) {
                        divProps['data-video-ids'] = '';
                        divProps['data-playlist-id'] = experienceOverrideModeIds.join(',');
                    }
                    break;
                case BrightcoveExperienceOverrideMode.Videos:
                    if (experienceOverrideModeIds) {
                        divProps['data-video-ids'] = experienceOverrideModeIds.join(',');
                        divProps['data-playlist-id'] = '';
                    }
                    break;
                default:
                    throw new Error('Unknown or unsupported Experience Override Mode');
            }
        }

        return (
            <>
                <div {...divProps} />
                <script src={scriptUrl} />
            </>
        );
    }

    render() {
        const { embedType, sizing } = this.props;

        switch (embedType) {
            case 'Standard':
                return this.renderIFrameFixed();
            case 'Advanced':
                switch (sizing) {
                    case 'Fixed':
                        return this.renderJavaScriptFixed();
                    case 'Responsive':
                        return this.renderJavaScriptResponsive();
                    default:
                        throw new Error('Unknown or unsupported embed sizing.');
                }
            default:
                throw new Error('Unknown or supported embed type.');
        }
    }
}

export function getDefaultBrightcoveExperienceProps(): Partial<BrightcoveExperienceProps> {
    return {
        connectorVersion: process.env.REACT_APP_VERSION,
        embedType: 'Advanced',
        sizing: 'Responsive',
        overrideWidth: true,
        playerWidth: '640',
        overrideHeight: true,
        playerHeight: '640',
        playerUnits: BrightcoveExperienceUnits.Pixels,
        overrideExperienceVideos: false,
        experienceOverrideMode: BrightcoveExperienceOverrideMode.Videos,
    };
}

export function getBrightcoveExperiencePropsFromQueryString(): Partial<BrightcoveExperienceProps> {
    const uri = URI();
    const info = uri.query(true) as { [key: string]: string };

    const result: Partial<BrightcoveExperienceProps> = {
        accountId: info.accountId,
        experienceId: info.experienceId,
        sharepointVersion: info.SPProductNumber,
        connectorVersion: process.env.REACT_APP_VERSION,
        embedType: 'Advanced',
        sizing: 'Responsive',
        overrideWidth: false,
        overrideHeight: false,
        overrideExperienceVideos: info.overrideExperienceVideos === '1' ? true : false,
    };

    if (info.experienceOverrideMode) {
        switch (info.experienceOverrideMode) {
            case '0':
                result.experienceOverrideMode = BrightcoveExperienceOverrideMode.Videos;
                break;
            case '1':
                result.experienceOverrideMode = BrightcoveExperienceOverrideMode.Playlist;
                break;
            default:
                result.experienceOverrideMode = BrightcoveExperienceOverrideMode.Videos;
        }
    }

    if (info.experienceOverrideModeIds) {
        result.experienceOverrideModeIds = info.experienceOverrideModeIds.split(',').map((id) => id.trim());
    }

    return result;
}

export interface BrightcoveExperienceProps {
    accountId: string;
    sharepointVersion: string;
    connectorVersion: string;
    experience?: BCExperience;
    experienceId: string;
    embedType: 'Standard' | 'Advanced';
    sizing: 'Responsive' | 'Fixed';
    overrideWidth: boolean;
    playerWidth?: string;
    overrideHeight: boolean;
    playerHeight?: string;
    playerUnits?: BrightcoveExperienceUnits;
    overrideExperienceVideos: boolean;
    experienceOverrideMode: BrightcoveExperienceOverrideMode;
    experienceOverrideModeIds?: Array<string>;
}


