import * as React from 'react';
import { observer } from 'mobx-react';

import { FormControl } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { Account } from '../../services/sharepoint';

import { find } from 'lodash';

@observer
export class AccountPicker extends React.Component<AccountPickerProps, AccountPickerState> {
    constructor(props: AccountPickerProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingAccounts: true,
        };
    }

    async componentDidMount() {
        const { availableAccounts, selectedAccount, onChange } = this.props;

        this.setState({
            isLoadingAccounts: true,
        });

        let newSelectedAccount = selectedAccount;
        if (!selectedAccount && availableAccounts && availableAccounts.length > 0) {
            newSelectedAccount = availableAccounts[0];
            if (onChange) {
                onChange(newSelectedAccount);
            }
        }

        this.setState({
            isLoadingAccounts: false,
        });
    }

    @autobind
    handleFieldChange(event: React.FormEvent<FormControl>) {
        const { availableAccounts, onChange } = this.props;
        const target = event.target as any;

        const selectedAccount = find(availableAccounts, { guid: target.value });

        if (selectedAccount && onChange) {
            onChange(selectedAccount);
        }
    }

    render() {
        const { disabled, className, availableAccounts, selectedAccount } = this.props;
        const isLoadingAccounts = false;

        if (isLoadingAccounts) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>Loading Accounts...</option>
                </FormControl>
            );
        }

        if (!availableAccounts || availableAccounts.length <= 0) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>There are no available accounts.</option>
                </FormControl>
            );
        }

        let selectedAccountGuid = '';
        if (selectedAccount) {
            selectedAccountGuid = selectedAccount.guid;
        }

        return (
            <FormControl
                className={className}
                componentClass="select"
                placeholder="Select an account..."
                value={selectedAccountGuid}
                onChange={this.handleFieldChange}
                disabled={disabled || false}
            >
                {availableAccounts.map((account, i) =>
                    <option key={i} value={account.guid}>{account.title}</option>
                )
                }
            </FormControl>
        );
    }
}

interface AccountPickerProps {
    className?: string;
    availableAccounts: Array<Account>;
    selectedAccount?: Account;
    onChange?: (selectedAccount: Account) => void;
    disabled?: boolean;
}

interface AccountPickerState {
}


