import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { Prompt } from 'react-router';
import autobind from 'autobind-decorator';
import { Spinner } from '../common';

import { BrightcoveApiGateway, BCVideo, BCPlaylist, BCPlaylistType } from '@brightcove/video-cloud-sdk';
import { SharePointApiGateway } from '../../services/sharepoint';

import { get, isEqual, cloneDeep, find, pull, pullAt, pullAllWith, differenceWith, map, findIndex } from 'lodash';

import './EditPlaylist.css';

const DefaultPlaylist: BCPlaylist = {
    name: 'New Playlist',
    description: '',
    favorite: false,
    id: '',
    reference_id: '',
    type: BCPlaylistType.Manual,
    video_ids: [],
    search: '',
    account_id: '',
};

export class EditPlaylist extends React.Component<EditPlaylistProps, EditPlaylistState> {
    constructor(props: EditPlaylistProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingPlaylist: true,
            isSavingPlaylist: false,
            playlistType: 'Manual',

            isLoadingAvailableVideos: false,
            availableVideosSearchTerms: '',
            availableVideos: [],
            selectedAvailableVideos: [],

            isLoadingPlaylistVideos: false,
            playlistVideos: [],
            selectedPlaylistVideos: [],

            playlist: cloneDeep(DefaultPlaylist),
            originalPlaylist: cloneDeep(DefaultPlaylist),

            showDeleteModal: false
        };
    }

    async componentDidMount() {

        const { bcApiGateway, accountId, playlistId } = this.props;

        if (playlistId) {
            // Get the existing playlist.
            const playlistsApi = bcApiGateway.playlistsApi;
            const playlist = await playlistsApi.getPlaylistById(accountId, playlistId);

            this.setState({
                isLoadingPlaylist: false,
                playlist: cloneDeep(playlist),
                originalPlaylist: playlist
            });
        } else {
            // New Playlist

            this.setState({
                isLoadingPlaylist: false,
            });
        }

        // Fire and forget video retrieval.
        this.getAvailableVideos();
        this.getVideosInPlaylist();
    }

    @autobind
    async getAvailableVideos() {
        const { bcApiGateway, accountId } = this.props;
        const { availableVideosSearchTerms } = this.state;

        this.setState({
            isLoadingAvailableVideos: true,
        });

        const videosApi = bcApiGateway.videosApi;
        const availableVideos = await videosApi.getVideos(accountId, {
            limit: 1000,
            sort: 'name',
            q: availableVideosSearchTerms
        });

        this.setState({
            isLoadingAvailableVideos: false,
            availableVideos,
            selectedAvailableVideos: []
        });
    }

    @autobind
    async getVideosInPlaylist() {
        const { bcApiGateway, accountId, playlistId } = this.props;

        if (!playlistId) {
            return;
        }

        this.setState({
            isLoadingPlaylistVideos: true,
        });

        const playlistsApi = bcApiGateway.playlistsApi;
        const playlistVideos = await playlistsApi.getVideosInPlaylist(accountId, playlistId);

        this.setState({
            isLoadingPlaylistVideos: false,
            playlistVideos,
            selectedPlaylistVideos: []
        });
    }

    @autobind
    async toggleSelectAvailableVideo(selectedVideo: BCVideo) {

        let { selectedAvailableVideos } = this.state;

        let containsVideo = find(selectedAvailableVideos, (video) => { return video.id === selectedVideo.id; });

        if (containsVideo) {
            pull(selectedAvailableVideos, containsVideo);
        } else {
            selectedAvailableVideos.push(selectedVideo);
        }

        this.setState({
            selectedAvailableVideos
        });
    }

    @autobind
    async toggleSelectPlaylistVideo(selectedVideo: BCVideo) {

        let { selectedPlaylistVideos } = this.state;

        let containsVideo = find(selectedPlaylistVideos, (video) => { return video.id === selectedVideo.id; });

        if (containsVideo) {
            pull(selectedPlaylistVideos, containsVideo);
        } else {
            selectedPlaylistVideos.push(selectedVideo);
        }

        this.setState({
            selectedPlaylistVideos
        });
    }

    @autobind
    addSelectedAvailableVideos() {
        let { playlist, playlistVideos, selectedAvailableVideos } = this.state;

        for (let selectedAvailableVideo of selectedAvailableVideos) {
            playlistVideos.push(selectedAvailableVideo);
        }

        playlist.video_ids = map(playlistVideos, 'id');

        this.setState({
            playlist,
            playlistVideos,
            selectedAvailableVideos: [],
            selectedPlaylistVideos: []
        });
    }

    @autobind
    removeSelectedPlaylistVideos() {
        let { playlist, playlistVideos, selectedPlaylistVideos } = this.state;

        playlistVideos = pullAllWith(playlistVideos, selectedPlaylistVideos, (v1, v2) => {
            return v1.id === v2.id;
        });

        playlist.video_ids = map(playlistVideos, 'id');

        this.setState({
            playlist,
            playlistVideos,
            selectedAvailableVideos: [],
            selectedPlaylistVideos: []
        });
    }

    @autobind
    moveSelectedPlaylistVideoUp() {
        let { playlist, playlistVideos, selectedPlaylistVideos } = this.state;

        if (selectedPlaylistVideos.length !== 1) {
            return;
        }

        const videoIndex = findIndex(playlistVideos, { id: selectedPlaylistVideos[0].id });
        if (videoIndex === 0) {
            return;
        }

        const videos = pullAt(playlistVideos, videoIndex);
        playlistVideos.splice(videoIndex - 1, 0, videos[0]);

        playlist.video_ids = map(playlistVideos, 'id');

        this.setState({
            playlist,
            playlistVideos,
        });
    }

    @autobind
    moveSelectedPlaylistVideoDown() {
        let { playlist, playlistVideos, selectedPlaylistVideos } = this.state;

        if (selectedPlaylistVideos.length !== 1) {
            return;
        }

        const videoIndex = findIndex(playlistVideos, { id: selectedPlaylistVideos[0].id });
        if (videoIndex === playlistVideos.length - 1) {
            return;
        }

        const videos = pullAt(playlistVideos, videoIndex);
        playlistVideos.splice(videoIndex + 1, 0, videos[0]);

        playlist.video_ids = map(playlistVideos, 'id');

        this.setState({
            playlist,
            playlistVideos,
        });
    }

    @autobind
    parseSearchField(): { tagsType: 'any' | 'all', tags: string } {
        // FIXME: this doesn't seem to work, nor does the search query described work.
        const { playlist } = this.state;
        const tagsRegex = /(\+?)tags:"(.*)"/g;

        if (!playlist.search || !playlist.search.match(tagsRegex)) {
            return {
                tagsType: 'any',
                tags: '',
            };
        }

        const matches = tagsRegex.exec(playlist.search);
        if (!matches) {
            return {
                tagsType: 'any',
                tags: '',
            };
        }

        return {
            tagsType: matches[1] === '+' ? 'all' : 'any',
            tags: matches[2]
        };
    }

    @autobind
    populateSearchField(tagsType: 'any' | 'all' | null, tags: string | null) {
        const { playlist } = this.state;
        let searchField = this.parseSearchField();
        if (tagsType !== null) {
            searchField.tagsType = tagsType;
        }

        if (tags !== null) {
            searchField.tags = tags;
        }

        if (searchField.tagsType === 'all') {
            playlist.search = `+tags:"${searchField.tags}"`;
        } else {
            playlist.search = `tags:"${searchField.tags}"`;
        }

        this.setState({
            playlist
        });
    }

    @autobind
    async previewSmartPlaylistVideos() {
        const { bcApiGateway, accountId } = this.props;
        const searchField = this.parseSearchField();

        this.setState({
            isLoadingPlaylistVideos: true,
            playlistVideos: []
        });

        const videosApi = bcApiGateway.videosApi;
        const availableVideos = await videosApi.getVideos(accountId, {
            limit: 30,
            sort: 'name',
            q: `${searchField.tags ? '+' : ''}tags:${searchField.tags}`
        });

        this.setState({
            isLoadingPlaylistVideos: false,
            playlistVideos: availableVideos
        });
    }

    @autobind
    showDeleteModal() {
        this.setState({
            showDeleteModal: true
        });
    }

    @autobind
    deleteCancel() {
        this.setState({
            showDeleteModal: false
        });
    }

    @autobind
    async deleteConfirm() {
        const { bcApiGateway, accountId, playlistId } = this.props;

        if (!playlistId) {
            return;
        }

        this.setState({
            isSavingPlaylist: true
        });

        const playlistsApi = bcApiGateway.playlistsApi;
        await playlistsApi.deletePlaylist(accountId, playlistId);
        window.location.hash = '#/manage-playlists';
    }

    @autobind
    handleInputChange(event: React.ChangeEvent<HTMLInputElement>) {
        const playlist = this.state.playlist;

        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        if (name === 'limit') {
            playlist.limit = parseInt(value.toString(), 10);
        } else {
            playlist[name] = value;
        }

        this.setState({
            playlist
        });
    }

    @autobind
    handlePlaylistTypeChange(event: React.ChangeEvent<HTMLSelectElement>) {
        const { playlist, originalPlaylist } = this.state;

        switch (event.target.value) {
            case 'Manual':
                playlist.type = BCPlaylistType.Manual;
                break;
            case 'Smart':
                if (originalPlaylist.type !== BCPlaylistType.Manual) {
                    playlist.type = originalPlaylist.type;
                } else {
                    playlist.type = BCPlaylistType.ActivatedDateNewestFirst;
                }
                break;
            default:
                playlist.type = event.target.value as BCPlaylistType;
                break;
        }

        this.setState({
            playlist
        });
    }

    @autobind
    handleAvailableVideosSearchTermsChange(event: React.ChangeEvent<HTMLInputElement>) {
        this.setState({
            availableVideosSearchTerms: event.target.value
        });
    }

    @autobind
    async createOrUpdatePlaylist() {
        const { bcApiGateway, accountId, playlistId } = this.props;
        const { playlist } = this.state;

        this.setState({
            isSavingPlaylist: true
        });

        const playlistsApi = bcApiGateway.playlistsApi;

        if (!playlistId) {
            await playlistsApi.createPlaylist(accountId, playlist);
        } else {
            await playlistsApi.updatePlaylist(accountId, playlistId, playlist);
        }

        window.location.hash = `#/manage-playlists`;
    }

    render() {
        const { playlistId, accountId } = this.props;
        const {
            isSavingPlaylist,
            isLoadingPlaylist,
            playlist,
            originalPlaylist,
            availableVideosSearchTerms,
            availableVideos,
            selectedAvailableVideos,
            playlistVideos,
            selectedPlaylistVideos,
        } = this.state;

        if (isSavingPlaylist) {
            return (
                <Spinner loadingText="Saving Playlist..." />
            );
        }

        if (isLoadingPlaylist) {
            return (
                <Spinner loadingText="Loading Playlist..." />
            );
        }

        const playlistType = playlist.type && playlist.type.toString() === 'EXPLICIT' ? 'Manual' : 'Smart';
        const isSaveDisabled = !!playlistId && isEqual(playlist, originalPlaylist);

        const unselectedAvailableVideos = differenceWith(availableVideos, playlist.video_ids || [], (v1, v2) => {
            return v1.id === v2;
        });

        const hasSelectedAvailableVideos = selectedAvailableVideos.length > 0;
        const hasSelectedPlaylistVideos = selectedPlaylistVideos.length > 0;

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li><a href="#/manage-playlists">Manage Playlists</a></li>
                    <li><a href="#/manage-playlists">Account {accountId}</a></li>
                    <li className="active">Playlist {playlistId}</li>
                </ol>
                <div>
                    <label>Name: </label>
                    <input name="name" type="text" className="form-control" value={playlist.name} onChange={this.handleInputChange} />

                    <label>Description: </label>
                    <input name="description" type="text" id="txtDescription" className="form-control" value={playlist.description || ''} onChange={this.handleInputChange} />

                    <label>Type: </label>
                    <select className="form-control" value={playlistType} onChange={this.handlePlaylistTypeChange}>
                        <option>Manual</option>
                        <option>Smart</option>
                    </select>
                    <hr />
                    {playlist.type && playlist.type.toString() === 'EXPLICIT' &&
                        <>
                            <div className="heading">Manual Playlist Settings</div>
                            <div className="row">
                                <div className="availableVideoSearch col-md-5">
                                    <label>Available Videos</label>
                                    <div className="input-group">
                                        <input
                                            type="search"
                                            placeholder="Search"
                                            className="form-control"
                                            value={availableVideosSearchTerms}
                                            onKeyPress={(e) => e.key === 'Enter' && this.getAvailableVideos()}
                                            onChange={this.handleAvailableVideosSearchTermsChange}
                                        />
                                        <div className="input-group-btn searchButton">
                                            <Button onClick={this.getAvailableVideos}><i className="glyphicon glyphicon-search" /></Button>
                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-2" />

                                <div className="playlistOrder col-md-5">
                                    <label>Videos in this Playlist</label>
                                    <br />
                                    <span style={{ paddingRight: '5px' }}>Move Video</span>
                                    <div className="buttons btn-group btn-group-sm">
                                        <Button disabled={selectedPlaylistVideos.length !== 1} onClick={this.moveSelectedPlaylistVideoUp}>Up</Button>
                                        <Button disabled={selectedPlaylistVideos.length !== 1} onClick={this.moveSelectedPlaylistVideoDown}>Down</Button>
                                    </div>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-md-5">
                                    <div className="availableVideos col-md-12">
                                        {unselectedAvailableVideos.map((video) => {
                                            const isSelected = !!find(selectedAvailableVideos, (v1) => { return v1.id === video.id; });
                                            const videoClass = isSelected ? 'video videoSelected row' : 'video row';
                                            const thumbnailSrc = get(video, 'images.thumbnail.src');
                                            return (
                                                <div key={video.id} className={videoClass} onClick={() => this.toggleSelectAvailableVideo(video)}>
                                                    <img className="resultImage col-md-6" src={thumbnailSrc} />
                                                    <div className="title col-md-6">
                                                        <div>{video.name}</div>
                                                        <div>video id: {video.id}</div>
                                                    </div>
                                                </div>
                                            );
                                        }
                                        )}
                                    </div>
                                </div>

                                <div className="overButtons col-md-2">
                                    <Button disabled={!hasSelectedAvailableVideos} onClick={this.addSelectedAvailableVideos}>&gt;&gt; </Button>
                                    <br />
                                    <Button disabled={!hasSelectedPlaylistVideos} onClick={this.removeSelectedPlaylistVideos}>&lt;&lt; </Button>
                                </div>

                                <div className="col-md-5">
                                    <div className="playlistVideos container col-md-12">
                                        {playlistVideos.map((video) => {
                                            const isSelected = !!find(selectedPlaylistVideos, (v1) => { return v1.id === video.id; });
                                            const videoClass = isSelected ? 'video videoSelected row' : 'video row';
                                            const thumbnailSrc = get(video, 'images.thumbnail.src');
                                            return (
                                                <div key={video.id} className={videoClass} onClick={() => this.toggleSelectPlaylistVideo(video)}>
                                                    <img className="resultImage col-md-6" src={thumbnailSrc} />
                                                    <div className="title col-md-6">
                                                        <div>{video.name}</div>
                                                        <div>video id:  {video.id}</div>
                                                    </div>
                                                </div>
                                            );
                                        }
                                        )}
                                    </div>
                                </div>
                            </div>
                        </>
                    }

                    {playlist.type && playlist.type.toString() !== 'EXPLICIT' &&
                        <>
                            <div className="heading">Smart Playlist Settings</div>
                            <div className="settingsText">Videos will be automatically added to this Playlist based on the following settings:</div>
                            <div className="row">
                                <div className="col-md-12">
                                    <label>Tags:</label>
                                    <select
                                        className="form-control"
                                        value={this.parseSearchField().tagsType}
                                        onChange={(e) => { this.populateSearchField(e.target.value === 'any' ? 'any' : 'all', null); }}
                                    >
                                        <option value="any">Contain one or more</option>
                                        <option value="all">Contain all</option>
                                    </select>
                                    <br />
                                    <label style={{ fontWeight: 'normal' }}>of the following:</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={this.parseSearchField().tags}
                                        onChange={(e) => this.populateSearchField(null, e.target.value)}
                                    />
                                    <br />
                                    <label>Order:</label>
                                    <select className="form-control" value={playlist.type} onChange={this.handlePlaylistTypeChange}>
                                        <option value="ACTIVATED_OLDEST_TO_NEWEST">Activated Date (Oldest First)</option>
                                        <option value="ACTIVATED_NEWEST_TO_OLDEST">Activated Date (Newest First)</option>
                                        <option value="ALPHABETICAL">Video Name (A-Z)</option>
                                        <option value="PLAYS_TOTAL">Total Plays</option>
                                        <option value="PLAYS_TRAILING_WEEK">Trailing Week Plays</option>
                                        <option value="START_DATE_OLDEST_TO_NEWEST">Start Date (Oldest First)</option>
                                        <option value="START_DATE_NEWEST_TO_OLDEST">Start Date (Newest First)</option>
                                    </select>
                                    <br />
                                    <label>Limit to: </label>
                                    <input name="limit" type="text" style={{ width: '50px' }} className="form-control" value={playlist.limit} onChange={this.handleInputChange} />
                                </div>
                                <Button className="pull-right" onClick={this.previewSmartPlaylistVideos}>Preview</Button>
                            </div>
                            <div className="row">
                                <div className="col-md-12">
                                    <label>Videos in this Playlist: </label>
                                    <br />
                                    <div className="availableVideos container col-md-12">
                                        {playlistVideos.map((video) => {
                                            const thumbnailSrc = get(video, 'images.thumbnail.src');
                                            return (
                                                <div key={video.id} className="video row">
                                                    <img className="resultImage col-md-5" src={thumbnailSrc} />
                                                    <div className="title col-md-7">
                                                        <div>{video.name}</div>
                                                        <div>video id: {video.id}</div>
                                                    </div>
                                                </div>
                                            );
                                        }
                                        )}
                                    </div>
                                </div>
                            </div>
                        </>
                    }
                </div>
                <hr />
                <div className="pull-right">
                    <Button
                        className="btn-space"
                        bsStyle={!isSaveDisabled ? 'primary' : 'default'}
                        disabled={isSaveDisabled}
                        onClick={this.createOrUpdatePlaylist}
                    >
                        {!playlistId ? 'Create' : 'Save'}
                    </Button>
                    <Button className="btn-space" href="#/manage-playlists">Cancel</Button>
                    <Button className="btn-space" bsStyle="warning" disabled={!playlistId} onClick={this.showDeleteModal}>Delete Playlist</Button>
                </div>
                <Modal show={this.state.showDeleteModal} onHide={this.deleteCancel}>
                    <Modal.Header closeButton={true}>
                        <Modal.Title>Confirm Playlist Deletion</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <h5>This playlist will be permanently deleted. Are you sure you want to proceed?</h5>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={this.deleteConfirm} bsStyle="danger">Delete Playlist</Button>
                        <Button onClick={this.deleteCancel}>Cancel</Button>
                    </Modal.Footer>
                </Modal>
                <Prompt
                    when={!isSaveDisabled}
                    message="There are unsaved changes. Are you sure you wish to leave?"
                />
            </>
        );
    }
}

interface EditPlaylistProps {
    bcApiGateway: BrightcoveApiGateway;
    spApiGateway: SharePointApiGateway;
    accountId: string;
    playlistId?: string;
}

interface EditPlaylistState {
    isLoadingPlaylist: boolean;
    isSavingPlaylist: boolean;

    isLoadingAvailableVideos: boolean;
    availableVideos: Array<BCVideo>;
    availableVideosSearchTerms: string;
    selectedAvailableVideos: Array<BCVideo>;

    isLoadingPlaylistVideos: boolean;
    playlistVideos: Array<BCVideo>;
    selectedPlaylistVideos: Array<BCVideo>;

    playlist: BCPlaylist;
    originalPlaylist: BCPlaylist;
    playlistType: 'Manual' | 'Smart';

    showDeleteModal: boolean;
}


