import { ConnectorContext, VideoSearchParameters, SortOrder, VideoSortField } from '..';
import { mapSPVideoToMVideo, ConnectorVideo } from '../ConnectorVideo';

import { filter, orderBy } from 'lodash';

export async function searchForSharePointVideos(this: ConnectorContext, searchParams: Partial<VideoSearchParameters>): Promise<Array<ConnectorVideo>> {
    const { spApiGateway, selectedAccount } = this;

    if (!selectedAccount) {
        return [];
    }

    const spVideos = await spApiGateway.videosList.getVideosByAccountName(selectedAccount.title);

    let result: Array<ConnectorVideo> = [];
    for (const spVideo of spVideos) {
        result.push(mapSPVideoToMVideo(spVideo));
    }

    if (searchParams.searchTerms) {
        const terms = searchParams.searchTerms.toLowerCase();
        result = filter(result, (r) => r.name && r.name.toLowerCase().indexOf(terms) > -1);
    }

    const sortDirection = searchParams.sortOrder === SortOrder.Ascending ? 'asc' : 'desc';

    // Perform a client-side search.
    switch (searchParams.sortField) {
        case VideoSortField.Name:
            result = orderBy(result, ['name'], [sortDirection]);
            break;
        case VideoSortField.TotalPlays:
            result = orderBy(result, ['guid'], [sortDirection]);
            break;
        case VideoSortField.CreationDate:
            result = orderBy(result, ['creationDate'], [sortDirection]);
            break;
        case VideoSortField.UpdatedDate:
            result = orderBy(result, ['updatedDate'], [sortDirection]);
            break;
        case VideoSortField.StartDate:
            result = orderBy(result, ['startDate'], [sortDirection]);
            break;
        default:
            // Do nothing.
            break;
    }

    return result;
}


