import * as React from 'react';
import { FormControl } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { BCPlayer } from '@brightcove/video-cloud-sdk';
import { ConnectorContext } from '../../services/connector';

import { get, find, filter, orderBy } from 'lodash';

export class PlayerPicker extends React.Component<PlayerPickerProps, PlayerPickerState> {
    constructor(props: PlayerPickerProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingPlayers: true,
            allPlayers: [],
            filteredPlayers: []
        };
    }

    async componentDidMount() {
        this.loadPlayers();
    }

    async componentDidUpdate(prevProps: PlayerPickerProps) {
        const { isLoadingPlayers, currentAccountId, currentPlayerFilter } = this.state;

        if (isLoadingPlayers) {
            return;
        }

        if (this.props.connectorContext.selectedAccount &&
            this.props.connectorContext.selectedAccount.accountId !== currentAccountId) {
            this.loadPlayers();
            return;
        }

        if (this.props.playerFilter !== currentPlayerFilter) {
            const result = this.filterPlayers(this.state.allPlayers);
            this.setState({
                filteredPlayers: result.filteredPlayers,
                selectedPlayer: result.selectedPlayer,
                currentPlayerFilter: this.props.playerFilter
            });
        }
    }

    @autobind
    async loadPlayers() {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;
        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingPlayers: true
        });

        const { playerFilter } = this.props;
        const { playerManagementApi } = connectorContext.bcApiGateway;

        const playersResponse = await playerManagementApi.getAllPlayers(selectedAccount.accountId);
        const allPlayers = playersResponse.items;
        const filterPlayerResult = this.filterPlayers(allPlayers);

        this.setState({
            isLoadingPlayers: false,
            allPlayers,
            filteredPlayers: filterPlayerResult.filteredPlayers,
            selectedPlayer: filterPlayerResult.selectedPlayer,
            currentAccountId: selectedAccount.accountId,
            currentPlayerFilter: playerFilter
        });
    }

    @autobind
    filterPlayers(allPlayers: Array<BCPlayer>): { filteredPlayers: Array<BCPlayer>, selectedPlayer?: BCPlayer } {
        const { playerFilter, playerType, defaultPlayerId, selectedPlayerId, onChange } = this.props;

        // If a player filter string is specified, filter the players by it.
        let filteredPlayers = allPlayers;
        if (playerFilter && playerFilter.length > 0) {
            const strFilter = playerFilter.toLowerCase();
            filteredPlayers = filter(filteredPlayers, (player) => player.name && player.name.toLowerCase().indexOf(strFilter) > -1);
        }

        // Filter players by selected type.
        filteredPlayers = filter(filteredPlayers, (player) => {
            let isPlaylistPlayer = false;

            const playlistSetting = get(player, 'branches.master.configuration.playlist');
            if (playlistSetting) {
                isPlaylistPlayer = true;
            }

            if (!isPlaylistPlayer) {
                const plugins = get(player, 'branches.master.configuration.plugins');
                if (plugins) {
                    const bcPlaylistUIPlayers = filter(plugins, { registry_id: '@brightcove/videojs-bc-playlist-ui' });
                    if (bcPlaylistUIPlayers.length > 0) {
                        isPlaylistPlayer = true;
                    }
                }
            }

            if (playerType === 'Video' && isPlaylistPlayer) {
                return false;
            }

            if (playerType === 'Playlist' && !isPlaylistPlayer) {
                return false;
            }

            return true;
        });

        // Sort the players alphabetically by name with the default player on top.
        filteredPlayers = orderBy(filteredPlayers, ['name']);
        const defaultPlayerIndex = filteredPlayers.findIndex((p) => p.id === 'default');
        if (defaultPlayerIndex > 0) {
            const defaultPlayer = filteredPlayers.splice(defaultPlayerIndex, 1);
            filteredPlayers.splice(0, 0, defaultPlayer[0]);
        }

        // Select the player based on the set we have now.
        let selectedPlayer: BCPlayer | undefined = undefined;
        if (filteredPlayers && filteredPlayers.length > 0) {
            selectedPlayer = find(filteredPlayers, { id: (selectedPlayerId || defaultPlayerId || 'default') });
            if (!selectedPlayer) {
                selectedPlayer = filteredPlayers[0];
            }
            if (selectedPlayer && onChange) {
                onChange(selectedPlayer);
            }
        }

        return {
            filteredPlayers,
            selectedPlayer,
        };
    }

    @autobind
    handlePlayerChange(event: React.FormEvent<FormControl>) {

        const { onChange } = this.props;
        const { allPlayers } = this.state;
        const target = event.target as any;

        const selectedPlayer = find(allPlayers, { id: target.value });

        if (selectedPlayer && onChange) {
            onChange(selectedPlayer);
        }
    }

    render() {
        const { placeholder, className, connectorContext, selectedPlayerId, playerFilter } = this.props;
        const { isLoadingPlayers, filteredPlayers } = this.state;

        if (!connectorContext.selectedAccount) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                    style={{width: '100%'}}
                >
                    <option>Please select an account...</option>
                </FormControl>
            );
        }

        if (isLoadingPlayers) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                    style={{width: '100%'}}
                >
                    <option>Loading Players...</option>
                </FormControl>
            );
        }

        if (!filteredPlayers && playerFilter && playerFilter.length > 0) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                    style={{width: '100%'}}
                >
                    <option>All players were filtered out</option>
                </FormControl>
            );
        }

        if (!filteredPlayers || filteredPlayers.length <= 0) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                    style={{width: '100%'}}
                >
                    <option>There are no players available for this content.</option>
                </FormControl>
            );
        }

        let bcPlaceholder = placeholder;
        if (!bcPlaceholder) {
            bcPlaceholder = 'Select a player...';
        }

        let bcPlayerId = selectedPlayerId;
        if (!bcPlayerId) {
            bcPlayerId = 'default';
        }

        return (
            <FormControl
                className={className}
                componentClass="select"
                placeholder={bcPlaceholder}
                value={bcPlayerId}
                onChange={this.handlePlayerChange}
                style={{width: '100%'}}
            >
                {filteredPlayers.map((player, i) =>
                    <option key={i} value={player.id}>{player.name}</option>
                )
                }
            </FormControl>
        );
    }
}

export interface PlayerPickerProps {
    className?: string;
    placeholder?: string;
    connectorContext: ConnectorContext;
    playerFilter?: string;
    playerType: 'Video' | 'Playlist';
    defaultPlayerId?: string;
    selectedPlayerId?: string;
    onChange?: (selectedPlayer: BCPlayer) => void;
}

export interface PlayerPickerState {
    isLoadingPlayers: boolean;
    currentAccountId?: string;
    currentPlayerFilter?: string;
    allPlayers: Array<BCPlayer>;
    filteredPlayers: Array<BCPlayer>;
    selectedPlayer?: BCPlayer;
}


