import { Web, sp, DateTimeFieldFormatType } from 'sp-pnp-js';
import { SPListDefinition } from './SPListDefinition';
import { isObject } from 'lodash';

export abstract class SPList<T> {
    private readonly _listDefinition: SPListDefinition;
    private readonly _addinWebUrl?: string;
    private readonly _hostWebUrl: string;
    private readonly _web: Web;

    private readonly _errorCallback?: (message: string, err?: Error) => boolean | undefined;

    private _hasEnsuredList = false;
    private _listId: string;

    constructor(listDefinition: SPListDefinition, hostWebUrl: string, addinWebUrl?: string, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
        this._listDefinition = listDefinition;
        this._hostWebUrl = hostWebUrl;
        this._addinWebUrl = addinWebUrl;

        if (!addinWebUrl) {
            this._web = new Web(hostWebUrl);
        } else {
            this._web = sp.crossDomainWeb(addinWebUrl, hostWebUrl);
        }

        this._errorCallback = errorCallback;
    }

    public get addinWebUrl(): string | undefined {
        return this._addinWebUrl;
    }

    public get hostWebUrl(): string {
        return this._hostWebUrl;
    }

    public get listDefinition(): SPListDefinition {
        return this._listDefinition;
    }

    public get web(): Web {
        return this._web;
    }

    /**
     * Creates a new item in the underlying list.
     */
    public async createNewItem(title?: string): Promise<T> {
        await this.ensureList();

        if (!title) {
            title = `New ${this.listDefinition.itemName}`;
        }

        try {
            let addItemResult = await this.web.lists.getById(this._listId)
                .items
                .add({
                    Title: title
                });

            let listItem = await addItemResult
                .item
                .select(this.getFieldNames())
                .get();

            return this.mapToObject(listItem);
        } catch (ex) {
            const err = this.raiseError(`Unable to create a new item in the list: ${ex}`);
            throw err;
        }
    }

    /**
     * Deletes the item with the specified id from the underlying list.
     * @param itemId 
     */
    public async deleteItem(itemId: string): Promise<void> {
        await this.ensureList();
        try {
            await this.web
                .lists
                .getById(this._listId)
                .items
                .getById(parseInt(itemId, 10))
                .delete();
        } catch (ex) {
            const err = this.raiseError(`Unable to delete item ${itemId} from the ${this.listDefinition.itemName} list: ${ex}`);
            throw err;
        }
    }

    /**
     * Returns a value that indicates if the list has been created.
     */
    public async ensureList(create?: boolean): Promise<boolean> {
        if (this._hasEnsuredList) {
            return true;
        }

        if (!create) {
            const lists = await this.web.lists.filter(`Title eq '${this.listDefinition.listName}'`).get();
            if (lists.length === 1) {
                this._hasEnsuredList = true;
                this._listId = lists[0].Id;
                return true;
            }
            return false;
        }

        // Ensure that we can retrieve the list.
        try {
            const listEnsureResult = await this.web.lists.ensure(this.listDefinition.listName);

            // Batching doesn' work cross-domain apparently.

            for (const fieldTitle of Object.keys(this.listDefinition.fieldMap)) {
                const fieldDefinition = this.listDefinition.fieldMap[fieldTitle];

                if (fieldDefinition.create === false) {
                    continue;
                }

                switch (fieldDefinition.type) {
                    case 'boolean':
                        await listEnsureResult
                            .list
                            .fields
                            .addBoolean(fieldTitle);
                        break;
                    case 'choice':
                        await listEnsureResult
                            .list
                            .fields
                            .addChoice(fieldTitle, fieldDefinition.choices || []);
                        break;
                    case 'date':
                        await listEnsureResult
                            .list
                            .fields
                            .addDateTime(fieldTitle, DateTimeFieldFormatType.DateOnly);
                        break;
                    case 'datetime':
                        await listEnsureResult
                            .list
                            .fields
                            .addDateTime(fieldTitle, DateTimeFieldFormatType.DateTime);
                        break;
                    case 'link':
                        await listEnsureResult
                            .list
                            .fields
                            .addUrl(fieldTitle);
                        break;
                    case 'multiline-text':
                        await listEnsureResult
                            .list
                            .fields
                            .addMultilineText(fieldTitle);
                        break;
                    case 'text':
                        await listEnsureResult
                            .list
                            .fields
                            .addText(fieldTitle);
                        break;
                    default:
                        throw new Error(`The specified field type is not implemented: ${fieldDefinition.type}`);
                }
            }

            this._hasEnsuredList = true;
        } catch (ex) {
            const err = this.raiseError(`Unable to retrieve the ${this.listDefinition.listName} List from ${this.listDefinition.listName}`);
            throw err;
        }

        return true;
    }

    /**
     * Gets a string that contains all field names.
     */
    public getFieldNames(): string {
        return Object.keys(this.listDefinition.fieldMap).join(',');
    }

    /**
     * Retrieves all items from the underlying list.
     */
    public async getItems(): Promise<Array<T>> {
        await this.ensureList();

        // Attempt to retrieve all list items.
        let listItems: Array<any>;
        try {
            listItems = await this.web
                .lists
                .getById(this._listId)
                .items
                .select(this.getFieldNames())
                .getAll();
        } catch (ex) {
            const err = this.raiseError(`Unable to retrieve ${this.listDefinition.itemNamePlural} from the ${this.listDefinition.itemName} list: ${ex}`);
            throw err;
        }

        let result = [];
        for (let listItem of listItems) {
            result.push(this.mapToObject(listItem));
        }

        return result;
    }

    public async getItemsByFieldValue(fieldName: string, fieldValue: string): Promise<Array<T>> {
        await this.ensureList();
        try {
            let listItems = await this.web
                .lists
                .getById(this._listId)
                .items
                .filter(`${fieldName} eq '${fieldValue.replace(`'`, `''`)}'`)
                .select(this.getFieldNames())
                .get();
            let result = [];
            for (let listItem of listItems) {
                result.push(this.mapToObject(listItem));
            }
            return result;
        } catch (ex) {
            const err = this.raiseError(`Unable to retrieve ${fieldName} ${fieldValue} from the ${this.listDefinition.listName} list: ${ex}`);
            throw err;
        }
    }

    /**
     * Retrieves a single item from the underlying list.
     */
    public async getItemById(itemId: string): Promise<T> {
        await this.ensureList();
        try {
            let listItem = await this.web
                .lists
                .getById(this._listId)
                .items
                .getById(parseInt(itemId, 10))
                .select(this.getFieldNames())
                .get();
            return this.mapToObject(listItem);
        } catch (ex) {
            const err = this.raiseError(`Unable to retrieve item ${itemId} from the ${this.listDefinition.listName} list: ${ex}`);
            throw err;
        }
    }

    /**
     * Returns the server relative url of the default view of the list.
     */
    public async getServerRelativeUrl() {
        await this.ensureList();
        const defaultView = await this.web.lists.getById(this._listId).defaultView.get();
        return defaultView.ServerRelativeUrl;
    }

    /**
     * Returns the server relative rl of the default view of the list.
     */
    public async getWebRelativeUrl() {
        await this.ensureList();
        const defaultView = await this.web.lists.getById(this._listId).defaultView.get();
        return defaultView.ServerRelativeUrl;
    }

    /**
     * Updates the specified item with the specified object in the underlyling list.
     * @param itemId 
     * @param item 
     */
    public async updateItem(itemId: string, item: Partial<T>): Promise<T> {
        await this.ensureList();
        try {
            const listItem = this.mapToListItem(item);
            let updateItemResult = await this.web
                .lists
                .getById(this._listId)
                .items
                .getById(parseInt(itemId, 10))
                .update(listItem);
            let updatedListItem = await updateItemResult.item.select(this.getFieldNames()).get();
            return this.mapToObject(updatedListItem);
        } catch (ex) {
            const err = this.raiseError(`Unable to update ${this.listDefinition.itemName} ${itemId} in the ${this.listDefinition.listName} list: ${ex}`);
            throw err;
        }
    }

    /**
     * Maps a list item to an object.
     * @param listItem 
     */
    protected mapToObject(listItem: any): T {
        let result = {};
        for (let key of Object.keys(listItem)) {
            let map = this.listDefinition.fieldMap[key];
            if (map !== undefined) {
                result[map.path] = listItem[key];
            }
        }

        return result as T;
    }

    /**
     * Maps an object to a list item.
     * @param obj 
     */
    protected mapToListItem(obj: Partial<T>): any {
        let objMap = {};
        for (let key of Object.keys(this.listDefinition.fieldMap)) {
            let map = this.listDefinition.fieldMap[key];
            objMap[map.path] = key;
        }

        let result = {};
        for (let key of Object.keys(obj)) {
            let map = objMap[key];
            if (map !== undefined) {
                const fieldDefinition = this.listDefinition.fieldMap[map];
                result[map] = obj[key];

                switch (fieldDefinition.type) {
                    case 'link':
                        if (isObject(result[map])) {
                            result[map].__metadata = { 'type': 'SP.FieldUrlValue' };
                        } else {
                            result[map] = undefined;
                        }
                        break;
                    case 'datetime':
                    case 'date':
                        if (!result[map]) {
                            result[map] = undefined;
                        }
                        break;
                    default:
                        // Do Nothing.
                        break;
                }
            }
        }

        return result;
    }

    /**
     * Raises an error to a callback optionally specified in the constructor.
     */
    protected raiseError(message: string, err?: Error): Error {

        const errorMessage = `${message} ${err && err.message ? err.message : ''}`;
        const outerErr = new Error(errorMessage);

        if (this._errorCallback) {
            const throwException = this._errorCallback(message, err);
            if (throwException === true) {
                throw outerErr;
            }
        } else {
            throw outerErr;
        }

        return outerErr;
    }
}


