import * as React from 'react';
import { observer } from 'mobx-react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow } from 'react-bootstrap-table';
import { AccountPicker, PlayerPicker, Spinner, CopyToClipboard } from '../common';

import { BrightcovePlayerControls, BrightcovePlayer, BrightcovePlayerProps, getDefaultBrightcovePlayerProps } from '../brightcove-player';
import { PlaylistSearchControls } from '../manage-playlists';
import { PlaylistSearchParameters, getDefaultPlaylistSearchParameters, SortOrder, PlaylistSortField } from '../../services/connector';

import autobind from 'autobind-decorator';

import { Account, SPUtility } from '../../services/sharepoint';
import { BCPlayer, BCPlaylist, BCGetPlaylistsParams } from '@brightcove/video-cloud-sdk';
import { ConnectorContext } from '../../services/connector';

import * as URI from 'urijs';

@observer
export class MiniPlaylists extends React.Component<MiniPlaylistsProps, MiniPlaylistsState> {
    constructor(props: MiniPlaylistsProps, context?: any) {
        super(props, context);

        let bcPlayerProps = getDefaultBrightcovePlayerProps();
        bcPlayerProps.mode = 'Playlist';
        bcPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

        this.state = {
            playerFilter: '',
            bcPlayerProps,
            searchParameters: getDefaultPlaylistSearchParameters(),
            isLoadingPlaylists: false,
            playlists: []
        };
    }

    componentDidMount() {
        this.searchForPlaylists(this.state.searchParameters);
    }

    @autobind
    async searchForPlaylists(searchParams: Partial<PlaylistSearchParameters>): Promise<void> {
        const { selectedAccount } = this.props.connectorContext;
        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingPlaylists: true
        });

        const { bcApiGateway } = this.props.connectorContext;
        const playlistsApi = bcApiGateway.playlistsApi;

        // Map search params to 'q'
        let bcParams: BCGetPlaylistsParams = {
            limit: searchParams.searchLimit || 45,
            sort: searchParams.sortOrder === SortOrder.Descending ? '-' : '',
            q: searchParams.searchTerms ? `+"${searchParams.searchTerms}"` : ''
        };

        switch (searchParams.sortField) {
            case PlaylistSortField.DisplayName:
                bcParams.sort += 'name';
                break;
            case PlaylistSortField.ModifiedDate:
                bcParams.sort += 'updated_at';
                break;
            default:
                // Do nothing.
                break;
        }

        const playlists = await playlistsApi.getPlaylists(selectedAccount.accountId, bcParams);

        this.setState({
            isLoadingPlaylists: false,
            playlists
        });
    }

    @autobind
    async handleAccountChange(newAccount: Account) {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        if (!selectedAccount || selectedAccount.accountId !== newAccount.accountId) {
            connectorContext.selectedAccount = newAccount;
            this.searchForPlaylists(this.state.searchParameters);
        }
    }

    @autobind
    handlePlayerFilterChange(value: string) {
        this.setState({
            playerFilter: value
        });
    }

    @autobind
    handlePlayerChange(selectedPlayer: BCPlayer) {
        this.setState({
            selectedPlayer
        });
    }

    @autobind
    handlePlayerEmbedCodeChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        this.setState({
            bcPlayerProps
        });
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<PlaylistSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForPlaylists(searchParameters);
        }
    }

    @autobind
    playlistSelected(playlist: BCPlaylist, isSelected: boolean, e: any) {
        let selectedPlaylist: BCPlaylist | undefined;
        if (isSelected) {
            selectedPlaylist = playlist;
        }
        this.setState({
            selectedPlaylist
        });
    }

    @autobind
    renderPlaylistNameCell(cell: any, playlist: BCPlaylist, extraData: { selectedAccount: Account }) {
        let uri = URI(`${window.location.origin}${window.location.pathname}${window.location.search}`);
        uri = uri.removeQuery('mini');
        uri = uri.removeQuery('IsDlg');

        const playlistUrl = `${uri.toString()}#/manage-playlists/edit/${extraData.selectedAccount.accountId}/playlist/${playlist.id}`;

        return (
            <div className="row">
                <div className="col-xs-12">
                    <a href={playlistUrl} target="_blank">{playlist.name}</a>
                    <div className="small">{playlist.id}</div>
                </div>
            </div>
        );
    }

    renderPlaylistEmbed(selectedAccount: Account, selectedPlayer: BCPlayer, selectedPlaylist: BCPlaylist, bcPlayerProps: Partial<BrightcovePlayerProps>) {

        const playerPreviewUrl = `https://players.brightcove.net/${selectedAccount.accountId}/${selectedPlayer.id}_default/index.html?playlistId=${selectedPlaylist.id}`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(
            <BrightcovePlayer accountId={selectedAccount.accountId} playerId={selectedPlayer && selectedPlayer.id} mode="Playlist" modeId={selectedPlaylist.id} {...bcPlayerProps as BrightcovePlayerProps} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');

        const labelClass = 'col-xs-3 control-label small';
        const controlClass = 'col-xs-9';

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Preview in browser</label>
                    <div className={controlClass + ' word-wrap'}>
                        <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                    </div>
                </div>
                <BrightcovePlayerControls
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-sm small"
                    whClass="col-xs-6"
                    bcPlayerProps={bcPlayerProps}
                    onPlayerPropsChanged={this.handlePlayerEmbedCodeChanged}
                />
                <div className="col-xs-12 small">
                    <div>Brightcove Snippet (Paste to Script Editor web part or publishing zone):</div>
                    <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={() => null} />
                    <CopyToClipboard />
                </div>
                <div className="col-xs-12 small" style={{ paddingTop: '5px' }}>
                    <Button className="pull-right" bsSize="small" onClick={() => SPUtility.closeParentDialog(false)}>Close Dialog</Button>
                </div>
            </>
        );

    }

    render() {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        const {
            selectedPlayer,
            playerFilter,
            bcPlayerProps,
            searchParameters,
            isLoadingPlaylists,
            playlists,
            selectedPlaylist
        } = this.state;

        const labelClass = 'col-xs-3 control-label small';
        const controlClass = 'col-xs-9';

        const selectRow: SelectRow = {
            mode: 'radio',
            clickToSelect: true,
            className: 'info',
            onSelect: this.playlistSelected
        };

        return (
            <div className="form-horizontal">
                <div className="form-group">
                    <label className={labelClass}>Brightcove Account:</label>
                    <div className={controlClass}>
                        <AccountPicker className="input-sm" availableAccounts={connectorContext.availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player Filter:</label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Search"
                                className="form-control input-sm"
                                value={playerFilter}
                                onChange={(e) => this.handlePlayerFilterChange(e.target.value)}
                            />
                            <div className="input-group-btn searchButton">
                                <Button bsSize="small"><i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player:</label>
                    <div className={controlClass}>
                        <PlayerPicker className="input-sm" connectorContext={connectorContext} defaultPlayerId={selectedAccount && selectedAccount.defaultPlaylistPlayerId} playerType="Playlist" playerFilter={playerFilter} selectedPlayerId={selectedPlayer && selectedPlayer.id} onChange={this.handlePlayerChange} />
                    </div>
                </div>
                <PlaylistSearchControls
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-xs small"
                    searchParameters={searchParameters}
                    onSearchParametersChanged={this.handleSearchParametersChanged}
                    onSearchForPlaylists={this.searchForPlaylists}
                />
                {isLoadingPlaylists && selectedAccount &&
                    <Spinner loadingText="Loading Playlists..." />
                }
                {!isLoadingPlaylists && selectedAccount &&
                    <div className="well" style={{ height: '250px', overflow: 'auto', margin: '0 15px' }} >
                        <BootstrapTable data={playlists} striped={true} bordered={false} selectRow={selectRow}>
                            <TableHeaderColumn dataField="name" dataFormat={this.renderPlaylistNameCell} formatExtraData={{ selectedAccount }}>Playlist</TableHeaderColumn>
                            <TableHeaderColumn dataField="id" isKey={true} hidden={true}>ID</TableHeaderColumn>
                        </BootstrapTable>
                        <hr />
                    </div>
                }
                {!isLoadingPlaylists && selectedAccount && selectedPlayer && selectedPlaylist &&
                    this.renderPlaylistEmbed(selectedAccount, selectedPlayer, selectedPlaylist, bcPlayerProps)
                }
            </div>
        );
    }
}

interface MiniPlaylistsProps {
    connectorContext: ConnectorContext;
}

interface MiniPlaylistsState {
    selectedPlayer?: BCPlayer;
    playerFilter: string;
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    searchParameters: Partial<PlaylistSearchParameters>;

    isLoadingPlaylists: boolean;
    playlists: Array<BCPlaylist>;
    selectedPlaylist?: BCPlaylist;
}


