import * as React from 'react';
import { HashRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { LoadingProgress } from './components/common';
import { EditPartVideo } from './components/part-video';
import { EditPartPlaylist } from './components/part-playlist';
import { EditPartExperience } from './components/part-experience';
import { BrightcovePlayer, BrightcovePlayerProps, getBrightcovePlayerPropsFromQueryString } from './components/brightcove-player';
import { BrightcoveExperience, BrightcoveExperienceProps, getBrightcoveExperiencePropsFromQueryString } from 'components/brightcove-experience';

import { ConnectorContext } from './services/connector';
import * as URI from 'urijs';

import './Part.css';

export class Part extends React.Component<PartProps, PartState> {
    constructor(props: PartProps, context?: any) {
        super(props, context);

        const uri = URI();
        const info = uri.query(true) as any;

        this.state = {
            isLoading: true,
            isEditing: info.editmode === '1' ? true : false,
            bcPlayerProps: getBrightcovePlayerPropsFromQueryString(),
            bcExperienceProps: getBrightcoveExperiencePropsFromQueryString(),
        };
    }

    async componentDidMount() {
        const { connectorContext } = this.props;

        this.setState({
            isLoading: true,
        });

        await connectorContext.initialize();

        this.setState({
            isLoading: false,
        });
    }

    renderPlayer(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        if (!bcPlayerProps.accountId || !bcPlayerProps.playerId || !bcPlayerProps.modeId) {
            return (
                <>
                    <p><strong>This Brightcove Player WebPart needs to be configured.</strong></p>
                    <p>Please edit this web part to select the Brightcove video content to display.</p>
                </>
            );
        }

        return (
            <BrightcovePlayer {...bcPlayerProps as BrightcovePlayerProps} />
        );
    }

    renderExperience(bcExperienceProps: Partial<BrightcoveExperienceProps>) {
        if (!bcExperienceProps.accountId || !bcExperienceProps.experienceId) {
            return (
                <>
                    <p><strong>This Brightcove Experience WebPart needs to be configured.</strong></p>
                    <p>Please edit this web part to select the Brightcove Experience to display.</p>
                </>
            );
        }

        return (
            <BrightcoveExperience {...bcExperienceProps as BrightcoveExperienceProps} />
        );
    }

    render() {
        const { connectorContext } = this.props;
        const { isLoading, isEditing, bcPlayerProps, bcExperienceProps } = this.state;

        let partStyle: React.CSSProperties = {
        };

        if (isLoading) {
            return (
                <div className="part" style={partStyle}>
                    <LoadingProgress loadingMessage="Loading Brightcove Web Part..." />
                </div>
            );
        }

        return (
            <Router>
                <div className="part" style={partStyle}>
                    <Switch>
                    <Route path="/part/playlist" exact={true}>
                            <>
                                {isEditing &&
                                    <EditPartPlaylist connectorContext={connectorContext} playerProps={bcPlayerProps} />
                                }
                                {!isEditing && this.renderPlayer(bcPlayerProps)}
                            </>
                        </Route>
                        <Route path="/part/video" exact={true}>
                            <>
                                {isEditing &&
                                    <EditPartVideo connectorContext={connectorContext} playerProps={bcPlayerProps} />
                                }
                                {!isEditing && this.renderPlayer(bcPlayerProps)}
                            </>
                        </Route>
                        <Route path="/part/experience" exact={true}>
                            <>
                                {isEditing &&
                                    <EditPartExperience connectorContext={connectorContext} experienceProps={bcExperienceProps} />
                                }
                                {!isEditing && this.renderExperience(bcExperienceProps)}
                            </>
                        </Route>
                        <Redirect to="/" />
                    </Switch>
                </div>
            </Router>
        );
    }
}

interface PartProps {
    connectorContext: ConnectorContext;
    raiseError: (message: string, err?: Error) => boolean;
}

interface PartState {
    isLoading: boolean;
    isEditing: boolean;
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    bcExperienceProps: Partial<BrightcoveExperienceProps>;
}



