import * as React from 'react';
import { FormControl } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { SPGroup } from '../../services/sharepoint';
import { ConnectorContext } from '../../services/connector';

export class GroupPicker extends React.Component<GroupPickerProps, GroupPickerState> {
    constructor(props: GroupPickerProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingGroups: true,
            allGroups: []
        };
    }

    async componentDidMount() {

        const { connectorContext } = this.props;

        // Get the groups from the host web.
        const allGroups = await connectorContext.spApiGateway.spGroups.getSiteGroups();

        this.setState({
            isLoadingGroups: false,
            allGroups
        });
    }

    @autobind
    handleFieldChange(event: React.FormEvent<FormControl>) {
        if (this.props.onChange) {
            this.props.onChange(event);
        }
    }

    render() {

        if (this.state.isLoadingGroups) {
            return (
                <div>Loading Groups...</div>
            );
        }

        return (
            <FormControl componentClass="select" placeholder="Select a group..." value={this.props.value} onChange={this.handleFieldChange}>
                <option value="" />
                {this.state.allGroups.map((group, i) =>
                    <option key={i} value={group.title}>{group.title}</option>
                )
                }
            </FormControl>
        );
    }
}

interface GroupPickerProps {
    connectorContext: ConnectorContext;
    value: string;
    onChange?: (event: React.FormEvent<FormControl>) => void;
}

interface GroupPickerState {
    isLoadingGroups: boolean;
    allGroups: Array<SPGroup>;
}


