import { ConnectorContext, ConnectorVideoUpload } from '..';
import axios from 'axios';
import { get } from 'lodash';

export async function performDynamicIngest(this: ConnectorContext, video: ConnectorVideoUpload, statusUpdateCallback?: (video: ConnectorVideoUpload) => void): Promise<void> {

    if (!video) {
        throw new Error('A video must be specified.');
    }

    const onError = (message: string, ex: Error) => {
        video.hasError = true;
        video.status = 'An error has occurred while creating the Brightcove Video: ' + ex.message;
        video.isUploading = false;
        video.isComplete = true;
        video.progress = 100;
        if (statusUpdateCallback) {
            statusUpdateCallback(video);
        }
    };

    const { dynamicIngestApi, videosApi, foldersApi } = this.bcApiGateway;

    // Start the upload process.

    // 1. Create a new video.
    video.status = `Creating Brightcove video for ${video.fileName}...`;
    video.progress = 10;
    if (statusUpdateCallback) {
        statusUpdateCallback(video);
    }

    try {
        const createdVideo = await videosApi.createVideo(video.account.accountId, video.fileName, video.tags);
        video.bcVideo = createdVideo;
    } catch (ex) {
        onError('An error has occurred while creating the Brightcove Video: ' + ex.message, ex);
        return;
    } finally {
        if (statusUpdateCallback) {
            statusUpdateCallback(video);
        }
    }

    // 1a. If a folder is specified, add the video to the folder.
    if (video.targetFolder && video.targetFolder.id) {
        video.status = `Adding video to folder ${video.targetFolder.name}...`;
        video.progress = 15;
        if (statusUpdateCallback) {
            statusUpdateCallback(video);
        }

        try {
            await foldersApi.addVideoToFolder(video.account.accountId, video.targetFolder.id, video.bcVideo.id);
        } catch (ex) {
            onError('An error has occurred while adding the video to a folder: ' + ex.message, ex);
            return;
        } finally {
            if (statusUpdateCallback) {
                statusUpdateCallback(video);
            }
        }
    }

    // 2. Get the AWS S3 Urls for the upload.
    video.status = `Getting AWS S3 Urls for ${video.bcVideo.id}...`;
    video.progress = 20;
    if (statusUpdateCallback) {
        statusUpdateCallback(video);
    }

    try {
        video.uploadUrls = await dynamicIngestApi.getUploadUrls(video.account.accountId, video.bcVideo.id, video.fileName);
    } catch (ex) {
        onError('An error has occurred while obtaining upload urls: ' + ex.message, ex);
        return;
    } finally {
        if (statusUpdateCallback) {
            statusUpdateCallback(video);
        }
    }

    // 3. Perform the upload to AWS S3.
    video.status = `Uploading '${video.fileName}' (${video.bcVideo.id}) to AWS (Initializing...)`;
    video.progress = 40;
    if (statusUpdateCallback) {
        statusUpdateCallback(video);
    }

    try {
        await axios({
            method: 'PUT',
            url: video.uploadUrls.signed_url,
            headers: {
                'Content-Type': '' // The content type in the signed url is blank, thus we must do the same.
            },
            data: video.file,
            onUploadProgress: (progress) => {
                if (!video.bcVideo) {
                    return;
                }
                const pctComplete = progress.loaded / progress.total;
                video.status = `Uploading '${video.fileName}' (${video.bcVideo.id}) to AWS (${(pctComplete * 100).toFixed(2)}%)...`;
                video.progress = 40 + (pctComplete * 40);
                if (statusUpdateCallback) {
                    statusUpdateCallback(video);
                }
            }
        });

        // const uploadResponse = await fetch(video.uploadUrls.signed_url, {
        //     method: 'PUT',
        //     credentials: 'omit',
        //     headers: {
        //         'Content-Type': ''
        //     },
        //     body: video.file
        // });
        // video.uploadResponse = uploadResponse;
    } catch (ex) {
        onError('An error has occurred uploading a video to AWS: ' + ex.message, ex);
        return;
    } finally {
        if (statusUpdateCallback) {
            statusUpdateCallback(video);
        }
    }

    // 4. Make an ingest request.
    video.status = `Making an ingest request for ${video.bcVideo.id}...`;
    video.progress = 80;
    if (statusUpdateCallback) {
        statusUpdateCallback(video);
    }

    try {
        const makeIngestResponse = await dynamicIngestApi.makeIngestRequest(video.account.accountId, video.bcVideo.id, video.uploadUrls.api_request_url);
        video.ingestResponse = makeIngestResponse;
    } catch (ex) {
        onError('An error has occurred while making an ingest request: ' + ex.message, ex);
        return;
    } finally {
        if (statusUpdateCallback) {
            statusUpdateCallback(video);
        }
    }

    video.status = 'Ingest request made. Finalizing file upload.';
    video.isUploading = false;
    video.progress = 90;
    if (statusUpdateCallback) {
        statusUpdateCallback(video);
    }

    try {
        const spVideo = await this.spApiGateway.videosList.createNewItem(video.bcVideo.id);
        await videosApi.updateVideo(video.account.accountId, video.bcVideo.id, {
            reference_id: `bcsp-${spVideo.guid}`,
            // original_filename: video.fileName
        });
    } catch (ex) {
        onError('An error has occurred while finalizing video upload: ' + ex.message, ex);
        return;
    }

    // 5. Wait until the video completes processing.
    video.status = 'File transfer completed. Video is now processing on Brightcove..';
    video.isUploading = false;
    video.progress = 95;
    if (statusUpdateCallback) {
        statusUpdateCallback(video);
    }

    try {
        await waitUntilVideoThumbnailUrlsExist(this, video.account.accountId, video.bcVideo.id);
    } catch (ex) {
        onError('An error has occurred while checking on ingest job status: ' + ex.message, ex);
        return;
    }

    video.status = 'Upload & Processing Complete!';
    video.isUploading = false;
    video.isComplete = true;
    video.progress = 100;
    if (statusUpdateCallback) {
        statusUpdateCallback(video);
    }
}

/**
 * Helper function that waits until the specified video ingest job completes *** Note: Does not work for legacy ingest accounts ***
 * @param connectorContext 
 * @param accountId 
 * @param videoId 
 * @param ingestJobId 
 * @param watchInterval 
 */
export function waitUntilVideoIngestJobCompletes(connectorContext: ConnectorContext, accountId: string, videoId: string, ingestJobId: string, watchInterval?: number): Promise<{}> {
    if (!watchInterval) {
        watchInterval = 10 * 1000;
    }
    const { videosApi } = connectorContext.bcApiGateway;

    const watchPromise = new Promise((resolve, reject) => {
        const monitorTimer = setInterval(
            async () => {
                try {
                    const ingestStatus = await videosApi.getVideoIngestJobStatus(accountId, videoId, ingestJobId);
                    if (ingestStatus.state === 'finished') {
                        clearInterval(monitorTimer);
                        resolve();
                    }
                } catch (ex) {
                    clearInterval(monitorTimer);
                    reject(ex);
                }
            },
            watchInterval
        );
    });

    return watchPromise;
}

/**
 * Helper function that waits until a video thumbnail exists by polling the assets api ***Note: Does not work for dynamic ingest accounts***
 * @param connectorContext 
 * @param accountId 
 * @param videoId 
 * @param watchInterval 
 */
export function waitUntilVideoThumbnailsExist(connectorContext: ConnectorContext, accountId: string, videoId: string, watchInterval?: number): Promise<{}> {
    if (!watchInterval) {
        watchInterval = 10 * 1000;
    }
    const { assetsApi } = connectorContext.bcApiGateway;

    const watchPromise = new Promise((resolve, reject) => {
        const monitorTimer = setInterval(
            async () => {
                try {
                    const thumbnail = await assetsApi.getThumbnailList(accountId, videoId);
                    if (thumbnail) {
                        clearInterval(monitorTimer);
                        resolve();
                    }
                } catch (ex) {
                    clearInterval(monitorTimer);
                    reject(ex);
                }
            },
            watchInterval
        );
    });

    return watchPromise;
}

/**
 * Helper function that waits until a video thumbnail exists by polling the video api and examining the thumbnails property.
 * @param connectorContext 
 * @param accountId 
 * @param videoId 
 * @param watchInterval 
 */
export function waitUntilVideoThumbnailUrlsExist (connectorContext: ConnectorContext, accountId: string, videoId: string, watchInterval?: number): Promise<{}> {
    if (!watchInterval) {
        watchInterval = 10 * 1000;
    }
    const { videosApi } = connectorContext.bcApiGateway;

    const watchPromise = new Promise((resolve, reject) => {
        const monitorTimer = setInterval(
            async () => {
                try {
                    const video = await videosApi.getVideoById(accountId, videoId);
                    const videoThumbnail = get(video, 'images.thumbnail');
                    const videoThumbnailSrc = get(video, 'images.thumbnail.src');

                    if (videoThumbnail && videoThumbnailSrc) {
                        clearInterval(monitorTimer);
                        resolve();
                    }

                } catch (ex) {
                    clearInterval(monitorTimer);
                    reject(ex);
                }
            },
            watchInterval
        );
    });

    return watchPromise;
}


