import { Web, FileAddResult, sp } from 'sp-pnp-js';

export class SPUtility {
    /**
     * Returns a promise that resolves when the specified SharePoint SOD script is loaded.
     * @param scriptFileName 
     * @param scriptUrl 
     */
    public static async ensureScriptLoaded(scriptFileName: string, scriptUrl: string): Promise<{}> {
        let resolvePromise: () => void;
        const scriptLoadedPromise = new Promise((resolve, reject) => {
            resolvePromise = resolve;
        });

        SP.SOD.registerSod(scriptFileName, scriptUrl);
        SP.SOD.executeFunc(scriptFileName, '', () => {
            resolvePromise();
        });

        return scriptLoadedPromise;
    }

    /**
     * Executes the pending query on the specified context and returns a promise that resolves when the specified context succeeds or fails.
     * @param context 
     */
    public static async executePendingQuery(context: SP.ClientContext): Promise<{}> {
        if (!context) {
            throw Error('A ClientContext must be specified.');
        }

        let resolveExecuteQuery: (value?: {} | PromiseLike<{}> | undefined) => void;
        let rejectExecuteQuery: (reason?: any) => void;
        const executeQueryPromise = new Promise((resolve, reject) => {
            resolveExecuteQuery = resolve;
            rejectExecuteQuery = reject;
        });
        context.executeQueryAsync(
            (sender: any, args: SP.ClientRequestSucceededEventArgs) => {
                resolveExecuteQuery(args);
            },
            (sender: any, args: SP.ClientRequestFailedEventArgs) => {
                const err = new Error(args.get_message());
                (err as any).spError = args;
                rejectExecuteQuery(err);
            }
        );

        return executeQueryPromise;
    }

    /**
     * Gets the web id of the addin web.
     * @param webUrl 
     */
    public static async getWebId(webUrl: string): Promise<string> {
        const web = new Web(webUrl);

        const webInfo = await web.get();

        return webInfo.Id;
    }

    /**
     * Uploads a file to the specified web in the specified folder with the specified filename.
     * @param webUrl 
     * @param folderRelativeUrl 
     * @param fileName 
     * @param file 
     */
    public static async uploadFile(webUrl: string, folderRelativeUrl: string, fileName: string, file: File): Promise<FileAddResult> {
        const web = new Web(webUrl);
        const addedFileInfo = await web.getFolderByServerRelativeUrl(folderRelativeUrl)
            .files
            .add(fileName, file);
        return addedFileInfo;
    }

    /**
     * Utility function to close the parent dialog when in a Custom Action HostWebDialog.
     * @param refresh 
     */
    public static closeParentDialog(refresh: boolean) {
        if (refresh) {
            parent.postMessage('CloseCustomActionDialogRefresh', '*');
        } else {
            parent.postMessage('CloseCustomActionDialogNoRefresh', '*');
        }
    }

    /**
     * Utility function to instruct the parent sharepoint window to navigate to a specific page.
     */
    public static closeAndRedirect(url: string) {
        let postMessage = parent.postMessage;
        if (!postMessage && (parent.document as any).postMessage) {
            postMessage = (parent.document as any).postMessage;
        }

        if (!postMessage) {
            return;
        }

        postMessage('NavigateParent=' + url, '*');
    }
}


