import * as React from 'react';
import { observer } from 'mobx-react';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { VideoEmbed } from '../embeds';
import { VideoSearchControls } from './VideoSearchControls';
import { AccountPicker, Spinner } from '../common';
import autobind from 'autobind-decorator';

import { Account } from '../../services/sharepoint';
import { ConnectorContext, ConnectorVideo } from '../../services/connector';
import { SearchTarget, VideoSearchParameters, getDefaultVideoSearchParameters } from '../../services/connector';

import { filter, startsWith } from 'lodash';

import './ManageVideos.css';

@observer
export class ManageVideos extends React.Component<ManageVideosProps, ManageVideosState> {
    constructor(props: ManageVideosProps, context?: any) {
        super(props, context);

        this.state = {
            searchParameters: getDefaultVideoSearchParameters(),
            isLoadingVideos: false,
            showEmbedModal: false
        };
    }

    @autobind
    async componentDidMount() {
        this.searchForVideos(this.state.searchParameters);
    }

    @autobind
    async searchForVideos(searchParams: Partial<VideoSearchParameters>): Promise<void> {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingVideos: true,
        });

        let videos: Array<ConnectorVideo>;
        switch (searchParams.searchTarget) {
            case SearchTarget.SharePoint:
                videos = await connectorContext.searchForSharePointVideos(searchParams);
                break;
            case SearchTarget.VideoCloud:
                videos = await connectorContext.searchForBrightcoveVideos(searchParams);
                break;
            default:
                return;
        }

        this.setState({
            isLoadingVideos: false,
            videos
        });
    }

    @autobind
    handleAccountChange(newAccount: Account) {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        if (!selectedAccount || selectedAccount.accountId !== newAccount.accountId) {
            connectorContext.selectedAccount = newAccount;
            this.searchForVideos(this.state.searchParameters);
        }
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<VideoSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForVideos(searchParameters);
        }
    }

    @autobind
    showEmbedModal(videoId: string) {
        this.setState({
            showEmbedModal: true,
            embedVideoId: videoId
        });
    }

    @autobind
    hideEmbedModal() {
        this.setState({
            showEmbedModal: false
        });
    }

    @autobind
    renderVideoCell(cell: any, video: ConnectorVideo, extraData: { selectedAccount: Account }): React.ReactElement<any> {
        const { selectedAccount } = extraData;
        const { searchTarget } = this.state.searchParameters;

        let videoLocation = 'Brightcove';
        if (searchTarget === SearchTarget.SharePoint) {
            videoLocation = 'SharePoint';
        }

        const editVideoUrl = `#/manage-videos/edit/${selectedAccount.accountId}/${videoLocation}/${video.videoId}`;
        const tagsToDisplay = filter(video.tags, (tag) => !startsWith(tag, 'bcsp-'));

        return (
            <div className="result">
                <img className="resultImage col-md-2" src={video.thumbnailUrl} />
                <div className="col-md-7">
                    <div><a href={editVideoUrl}>{video.name}</a></div>
                    {video.shortDescription &&
                        <div className="short-description">{video.shortDescription}</div>
                    }
                    {!video.complete &&
                        <div className="form-group">
                            <div className="alert alert-warning small" style={{padding: '5px'}} role="alert">This video is still processing on the Video Cloud. Renditions may not be available. This might also be a Live event with no thumbnail image.</div>
                        </div>
                    }
                    <div>
                        {tagsToDisplay.map((tag, i) =>
                            <Button key={i} bsSize="xs" bsStyle="success">{tag}</Button>
                        )}
                    </div>
                </div>
                <div className="col-md-3 text-center">
                    <div>
                        <a href={editVideoUrl}>Edit Video</a>
                    </div>
                    <div className="video-id">Video Id: {video.videoId}</div>
                    <div>
                        <Button
                            bsStyle="success"
                            bsSize="small"
                            style={{ width: '90px' }}
                            onClick={() => this.showEmbedModal(video.videoId)}
                        >
                            Embed
                        </Button>
                    </div>
                </div>
            </div>
        );
    }

    render() {

        const { connectorContext } = this.props;

        const {
            isLoadingVideos,
            searchParameters,
            videos,
            showEmbedModal,
            embedVideoId
        } = this.state;

        const labelClass = 'col-md-3 control-label';
        const controlClass = 'col-md-9';

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li className="active">Manage Videos</li>
                </ol>
                <div className="search form-horizontal">
                    <div className="form-group">
                        <label className={labelClass}>Select Account:</label>
                        <div className={controlClass}>
                            <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccount={connectorContext.selectedAccount} onChange={this.handleAccountChange} />
                        </div>
                    </div>
                </div>
                <div className="search form-horizontal">
                    {connectorContext.selectedAccount &&
                        <>
                            <VideoSearchControls
                                connectorContext={connectorContext}
                                showSearchTarget={false}
                                labelClass={labelClass}
                                controlClass={controlClass}
                                searchParameters={searchParameters}
                                onSearchParametersChanged={this.handleSearchParametersChanged}
                                onSearchForVideos={this.searchForVideos}
                            />
                            {isLoadingVideos &&
                                <Spinner loadingText="Loading Videos..." />
                            }
                            {!isLoadingVideos && videos &&
                                <BootstrapTable data={videos} striped={true} bordered={false} pagination={videos.length > 10} options={{ paginationPosition: 'bottom' }}>
                                    <TableHeaderColumn dataField="videoId" isKey={true} dataFormat={this.renderVideoCell} formatExtraData={{ selectedAccount: connectorContext.selectedAccount }}>Results</TableHeaderColumn>
                                </BootstrapTable>
                            }
                            {embedVideoId &&
                                <Modal className="embed" show={showEmbedModal} onHide={this.hideEmbedModal} bsSize="large">
                                    <Modal.Header closeButton={true}>
                                        <Modal.Title>Embed Video</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <VideoEmbed
                                            connectorContext={connectorContext}
                                            account={connectorContext.selectedAccount}
                                            videoId={embedVideoId}
                                        />
                                    </Modal.Body>
                                    <Modal.Footer />
                                </Modal>
                            }
                        </>
                    }
                </div>
            </>
        );
    }
}

interface ManageVideosProps {
    connectorContext: ConnectorContext;
}

interface ManageVideosState {
    searchParameters: Partial<VideoSearchParameters>;

    isLoadingVideos: boolean;
    videos?: Array<ConnectorVideo>;

    showEmbedModal: boolean;
    embedVideoId?: string;
}


