import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import autobind from 'autobind-decorator';
import { Button } from 'react-bootstrap';
import { PlayerPicker, CopyToClipboard } from '../common';

import { BrightcovePlayer, BrightcovePlayerProps, BrightcovePlayerControls, BrightcovePlayerControlsProps, getDefaultBrightcovePlayerProps } from '../brightcove-player';

import { BCPlayer } from '@brightcove/video-cloud-sdk';
import { ConnectorContext } from '../../services/connector';
import { Account } from '../../services/sharepoint';

export class VideoEmbed extends React.Component<VideoEmbedProps, VideoEmbedState> {
    constructor(props: VideoEmbedProps, context?: any) {
        super(props, context);

        let bcPlayerProps = getDefaultBrightcovePlayerProps();
        bcPlayerProps.mode = 'Video';
        bcPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

        this.state = {
            playerFilter: '',
            bcPlayerProps
        };
    }
    
    @autobind
    handlePlayerFilterChange(value: string) {
        this.setState({
            playerFilter: value
        });
    }

    @autobind
    async handlePlayerChange(newPlayer: BCPlayer) {

        // Get embeds for the player.
        // const { bcApiGateway, accountId } = this.props;
        // const embeds = await bcApiGateway.playerManagementApi.getPlayerEmbeds(accountId, newPlayer.id);
        
        this.setState({
            selectedPlayer: newPlayer
        });
    }

    @autobind
    handlePlayerPropsChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        this.setState({
            bcPlayerProps
        });
    }

    render() {
        const { connectorContext, account, videoId } = this.props;
        const { playerFilter, selectedPlayer, bcPlayerProps } = this.state;

        const playerPreviewUrl = `https://players.brightcove.net/${account.accountId}/${selectedPlayer ? selectedPlayer.id : 'default'}_default/index.html?videoId=${videoId}`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(
            <BrightcovePlayer accountId={account.accountId} playerId={selectedPlayer && selectedPlayer.id} mode="Video" modeId={videoId} {...bcPlayerProps as BrightcovePlayerProps} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');

        return (
            <>
                <div className="form-horizontal">
                    <div className="form-group">
                        <label>Player Filter:</label>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Search"
                                className="form-control input-sm"
                                value={playerFilter}
                                onChange={(e) => this.handlePlayerFilterChange(e.target.value)}
                            />
                            <div className="input-group-btn searchButton">
                                <Button bsSize="small"><i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                    <div className="form-group">
                        <label>Select Player</label>
                        <PlayerPicker
                            connectorContext={connectorContext}
                            playerType="Video"
                            playerFilter={playerFilter}
                            defaultPlayerId={account.defaultVideoPlayerId}
                            selectedPlayerId={selectedPlayer && selectedPlayer.id}
                            onChange={this.handlePlayerChange}
                        />
                    </div>
                </div>
                {selectedPlayer &&
                    <div className="form-horizontal">
                        <div className="form-group">
                            {selectedPlayer.id === 'default' &&
                                <label>This is an instance of the Brightcove Default Player. You can modify this or choose to create your own.</label>
                            }
                            <div>
                                <iframe
                                    style={{ width: '100%', height: '400px' }}
                                    src={`//players.brightcove.net/${account.accountId}/${selectedPlayer.id}_default/index.html?videoId=${videoId}`}
                                />
                            </div>
                        </div>
                        <div className="form-group">
                            <label>Preview in browser</label>
                            <div>
                                <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                            </div>
                        </div>
                    </div>
                }
                {selectedPlayer &&
                    <>
                        <div className="row">
                            <div className="col-md-6">
                                <h4>Copy Code</h4>
                                <div>Copy the appropriate code below and paste it into your web page or app.</div>
                                <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={() => null} />
                                <CopyToClipboard />
                            </div>
                            <div className="col-md-6">
                                Change Player Size
                                <BrightcovePlayerControls bcPlayerProps={bcPlayerProps} onPlayerPropsChanged={this.handlePlayerPropsChanged} />
                            </div>
                        </div>
                    </>
                }
            </>
        );
    }
}

export interface VideoEmbedProps {
    connectorContext: ConnectorContext;
    account: Account;
    videoId: string;
}

export interface VideoEmbedState {
    playerFilter: string;
    selectedPlayer?: BCPlayer;
    bcPlayerProps: Partial<BrightcovePlayerProps>;
}


