import { SPAddinContext, AccountsList, BrightcoveSettingsList, VideosList, SPGroups, SPEmbedUtil } from 'services/sharepoint';

export class SharePointApiGateway {
    private readonly _spAddinContext: SPAddinContext;
    private readonly _spGroups: SPGroups;
    private readonly _spEmbedUtil: SPEmbedUtil;

    private readonly _accountsList: AccountsList;
    private readonly _brightcoveSettingsList: BrightcoveSettingsList;
    private readonly _videosList: VideosList;

    private readonly _errorCallback?: (message: string, err?: Error) => boolean | undefined;

    public constructor(spAddinContext: SPAddinContext, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
        if (!spAddinContext) {
            throw new Error('spAddinContext must be specified.');
        }

        this._spAddinContext = spAddinContext;
        this._errorCallback = errorCallback;

        const spHostUrl: string = this._spAddinContext.SPHostUrl;
        let spAddinUrl: string | undefined = this._spAddinContext.SPAppWebUrl;

        if (spHostUrl === location.origin) {
            spAddinUrl = undefined;
        }

        this._accountsList = new AccountsList(spHostUrl, spAddinUrl, this._errorCallback);
        this._brightcoveSettingsList = new BrightcoveSettingsList(spHostUrl, spAddinUrl, this._errorCallback);
        this._videosList = new VideosList(spHostUrl, spAddinUrl, this._errorCallback);

        this._spGroups = new SPGroups(spHostUrl, spAddinUrl);
        this._spEmbedUtil = new SPEmbedUtil(spHostUrl, spAddinUrl);
    }

    get accountsList(): AccountsList {
        return this._accountsList;
    }

    get brightcoveSettingsList(): BrightcoveSettingsList {
        return this._brightcoveSettingsList;
    }

    get videosList(): VideosList {
        return this._videosList;
    }

    get spAddinContext(): SPAddinContext {
        return this._spAddinContext;
    }

    get spGroups(): SPGroups {
        return this._spGroups;
    }

    get spEmbedUtil(): SPEmbedUtil {
        return this._spEmbedUtil;
    }
}


