import * as React from 'react';
import { Button, Modal, ControlLabel, FormControl } from 'react-bootstrap';
import { GroupPicker } from '../common';
import autobind from 'autobind-decorator';

import { truncate } from 'lodash';
import { ConnectorContext } from '../../services/connector';

export class AccountFieldRow extends React.Component<AccountFieldRowProps, AccountFieldRowState> {
    constructor(props: AccountFieldRowProps, context?: any) {
        super(props, context);

        this.state = {
            showFieldModal: false,
            newFieldValue: props.fieldValue || ''
        };
    }

    @autobind
    showFieldModal() {
        this.setState({
            showFieldModal: true
        });
    }

    @autobind
    hideFieldModal() {
        this.setState({
            showFieldModal: false,
            newFieldValue: this.props.fieldValue || ''
        });
    }

    @autobind
    handleFieldChange(event: React.ChangeEvent<HTMLInputElement>) {
        this.setState({
            newFieldValue: event.target.value
        });
    }

    @autobind
    handleFieldChange2(event: React.FormEvent<FormControl>) {

        let target = event.target as any;

        this.setState({
            newFieldValue: target.value
        });
    }

    @autobind
    notifyUpdateFieldUpdate() {
        this.props.updateFieldValue(this.state.newFieldValue);
        this.setState({
            showFieldModal: false
        });
    }

    render() {
        const { fieldName, fieldDescription, fieldValueType, fieldValue, connectorContext } = this.props;
        const { showFieldModal, newFieldValue } = this.state;
        return (
            <tr>
                <td className="NameField">
                    <Button
                        className="mLinks"
                        bsStyle="primary"
                        bsSize="large"
                        onClick={this.showFieldModal}
                    >
                        {fieldName}
                    </Button>
                </td>
                <td>
                    {fieldValue &&
                        <Button
                            className="mLinks"
                            bsStyle="primary"
                            bsSize="large"
                            onClick={this.showFieldModal}
                        >
                            {truncate(fieldValue, { length: 30 })}
                        </Button>
                    }
                </td>
                {fieldDescription &&
                    <td>{fieldDescription}</td>
                }
                <Modal show={showFieldModal} onHide={this.hideFieldModal}>
                    <Modal.Header closeButton={true}>
                        <Modal.Title>{fieldName}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        {(!fieldValueType || fieldValueType === 'text') &&
                            <>
                                <ControlLabel>Value</ControlLabel>
                                <FormControl type="text" value={newFieldValue} placeholder="Enter Value" onChange={this.handleFieldChange2} />
                            </>
                        }
                        {(fieldValueType === 'group') && connectorContext &&
                            <>
                                <ControlLabel>{fieldName}</ControlLabel>
                                <GroupPicker connectorContext={connectorContext} value={newFieldValue} onChange={this.handleFieldChange2} />
                            </>
                        }
                        <br />
                        <h4>Description</h4>
                        <span>{fieldDescription}</span>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={this.hideFieldModal} bsStyle="default">Close</Button>
                        <Button onClick={this.notifyUpdateFieldUpdate} bsStyle="primary">Ok</Button>
                    </Modal.Footer>
                </Modal>
            </tr >
        );
    }
}

interface AccountFieldRowProps {
    fieldName: string;
    fieldDescription?: string;
    fieldValueType?: 'text' | 'group';
    fieldValue?: string | null;
    connectorContext?: ConnectorContext;
    updateFieldValue: (newValue: string) => void;
    
}

interface AccountFieldRowState {
    showFieldModal: boolean;
    newFieldValue: string;
}


