import * as React from 'react';
import { Button } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { PlaylistSortField, SortOrder, PlaylistSearchParameters } from '../../services/connector';

export class PlaylistSearchControls extends React.Component<PlaylistSearchControlsProps> {

    @autobind
    async handleSearchTermsChange(e: React.ChangeEvent<HTMLInputElement>) {
        const { searchParameters, onSearchParametersChanged } = this.props;
        searchParameters.searchTerms = e.target.value;

        onSearchParametersChanged(searchParameters, 'searchTerms');
    }

    @autobind
    async handleSortFieldChanged(newSortField: PlaylistSortField) {
        const { searchParameters, onSearchParametersChanged } = this.props;
        searchParameters.sortField = newSortField;

        onSearchParametersChanged(searchParameters);
    }

    @autobind
    async handleSortOrderChanged(newSortOrder: SortOrder) {
        const { searchParameters, onSearchParametersChanged } = this.props;
        searchParameters.sortOrder = newSortOrder;

        onSearchParametersChanged(searchParameters);
    }

    render() {
        const { searchParameters, onSearchForPlaylists, labelClass, controlClass, formControlClass } = this.props;
        const { searchTerms, sortField, sortOrder } = searchParameters;

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Available Playlists:</label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Playlist Filter"
                                className={`form-control ${formControlClass}`}
                                value={searchTerms}
                                onKeyPress={(e) => e.key === 'Enter' && onSearchForPlaylists(searchParameters)}
                                onChange={this.handleSearchTermsChange}
                            />
                            <div className="input-group-btn">
                                <Button bsSize={formControlClass === 'input-xs' ? 'small' : undefined} onClick={() => onSearchForPlaylists(searchParameters)}><i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Sort results by: </label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <label className="radio-inline">
                                <input type="radio" checked={sortField === PlaylistSortField.DisplayName} onChange={() => this.handleSortFieldChanged(PlaylistSortField.DisplayName)} />display name
                            </label>
                            <label className="radio-inline">
                                <input type="radio" checked={sortField === PlaylistSortField.ModifiedDate} onChange={() => this.handleSortFieldChanged(PlaylistSortField.ModifiedDate)} />modified date
                            </label>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Order: </label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <label className="radio-inline">
                                <input type="radio" checked={sortOrder === SortOrder.Ascending} onChange={() => this.handleSortOrderChanged(SortOrder.Ascending)} />ascending
                            </label>
                            <label className="radio-inline">
                                <input type="radio" checked={sortOrder === SortOrder.Descending} onChange={() => this.handleSortOrderChanged(SortOrder.Descending)} />descending
                            </label>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export interface PlaylistSearchControlsProps {
    labelClass?: string;
    controlClass?: string;
    formControlClass?: string;
    searchParameters: Partial<PlaylistSearchParameters>;
    onSearchParametersChanged: (searchParameters: Partial<PlaylistSearchParameters>, fieldUpdated?: string) => void;
    onSearchForPlaylists: (searchParameters: Partial<PlaylistSearchParameters>) => void;
}


