import * as React from 'react';
import { FormControl } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { BCFolder } from '@brightcove/video-cloud-sdk';
import { ConnectorContext } from '../../services/connector';

import { find, orderBy } from 'lodash';

export class FolderPicker extends React.Component<FolderPickerProps, FolderPickerState> {
    constructor(props: FolderPickerProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingFolders: true,
            allFolders: []
        };
    }

    async componentDidMount() {
        this.loadFolders();
    }

    async componentDidUpdate(prevProps: FolderPickerProps) {
        const { isLoadingFolders, currentAccountId } = this.state;

        if (isLoadingFolders) {
            return;
        }

        if (this.props.connectorContext.selectedAccount &&
            this.props.connectorContext.selectedAccount.accountId !== currentAccountId) {
            this.loadFolders();
        }
    }

    @autobind
    async loadFolders() {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;
        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingFolders: true,
        });

        // Get the folders.
        const allFolders = await connectorContext.bcApiGateway.foldersApi.getFolders(selectedAccount.accountId);

        this.setState({
            isLoadingFolders: false,
            allFolders: orderBy(allFolders, ['name']),
            currentAccountId: selectedAccount.accountId
        });
    }

    @autobind
    handleFieldChange(event: React.FormEvent<FormControl>) {
        const { onChange } = this.props;
        const { allFolders } = this.state;

        const target = event.target as any;

        const selectedFolder = find(allFolders, { id: target.value });

        if (onChange) {
            onChange(selectedFolder);
        }
    }

    render() {
        const { className, placeholder, connectorContext, selectedFolderId } = this.props;
        const { selectedAccount } = connectorContext;
        const { isLoadingFolders, allFolders } = this.state;

        if (!selectedAccount) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>Please select an account...</option>
                </FormControl>
            );
        }

        if (isLoadingFolders) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>Loading Folders...</option>
                </FormControl>
            );
        }

        return (
            <FormControl
                className={className}
                componentClass="select"
                placeholder={placeholder}
                value={selectedFolderId}
                onChange={this.handleFieldChange}
            >
                <option value="" />
                {allFolders.map((folder, i) =>
                    <option key={i} value={folder.id}>{folder.name}</option>
                )
                }
            </FormControl>
        );
    }
}

interface FolderPickerProps {
    className?: string;
    placeholder?: string;
    connectorContext: ConnectorContext;
    selectedFolderId?: string;
    onChange?: (selectedFolder?: BCFolder) => void;
}

interface FolderPickerState {
    isLoadingFolders: boolean;
    currentAccountId?: string;
    allFolders: Array<BCFolder>;
}


