import { BCAccessTokenService, BCApiCredentials, BCAccessToken } from '@brightcove/video-cloud-sdk';
import * as moment from 'moment';
import { SharePointApiGateway } from './sharepoint';
import { endsWith } from 'lodash';

/*
 * See:
 * https://support.brightcove.com/getting-access-tokens
 * https://brightcovelearning.github.io/Brightcove-API-References/oauth-api/v4/doc/index.html#api-Access_Token
*/

/**
 * Represents an Access Token Service that obtains an access token through a proxy.
 */
export class BCSPAccessTokenService implements BCAccessTokenService {
    private readonly _spApiGateway: SharePointApiGateway;
    private _proxyUrl: string | undefined;

    private _accessTokens: { [accountId: string]: { accessToken?: BCAccessToken, apiCredentials: BCApiCredentials, clearAccessToken?: NodeJS.Timer; } };
    private readonly _errorCallback?: (message: string, err?: Error) => boolean | undefined;

    constructor(spApiGateway: SharePointApiGateway, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
        this._accessTokens = {};
        this._spApiGateway = spApiGateway;

        this._errorCallback = errorCallback;
    }

    public async obtainAccessToken(accountId: string): Promise<BCAccessToken> {
        if (!accountId) {
            throw new Error('Account Id must be specified.');
        }

        // Get the proxy url if it hasn't already been retrieved.
        if (!this._proxyUrl) {
            try {
                const brightcoveSettings = await this._spApiGateway.brightcoveSettingsList.getBrightcoveSettings();
                let proxyUrl = brightcoveSettings.proxyUrl;

                if (process.env.REACT_APP_BC_Override_ProxyUrl === 'true') {
                    proxyUrl = process.env.REACT_APP_BC_ProxyUrl || `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_BC_Proxy_Port || '3001'}`;
                }

                if (!proxyUrl) {
                    const err = this.raiseError(`A proxy url could not be obtained from the settings list.`);
                    throw err;
                }

                if (!endsWith(proxyUrl, '/')) {
                    proxyUrl += '/';
                }

                this._proxyUrl = proxyUrl;
            } catch (ex) {
                const err = this.raiseError(`An error occurred while attempting to retrieve brightcove settings: ${ex}`);
                throw err;
            }
        }

        try {
            // If the account hasn't been defined, retrieve it.
            if (!this._accessTokens[accountId]) {
                const accountsList = this._spApiGateway.accountsList;
                const account = await accountsList.getAccountByAccountId(accountId);

                this._accessTokens[accountId] = {
                    apiCredentials: {
                        client_id: account.clientId,
                        client_secret: account.clientSecret
                    }
                };
            } else {
                const existingAccessToken = this._accessTokens[accountId].accessToken;
                if (existingAccessToken &&
                    existingAccessToken.expiration &&
                    moment(existingAccessToken.expiration).isAfter(moment())) {
                    return existingAccessToken;
                }
            }
        } catch (ex) {
            const err = this.raiseError(`An error occurred while attempting to retrieve account settings: ${ex}`);
            throw err;
        }

        try {
            const accessTokenInfo = this._accessTokens[accountId];

            const response = await fetch(`${this._proxyUrl}api/auth?v=${(new Date()).getTime()}`, {
                method: 'POST',
                mode: 'cors',
                credentials: 'omit',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
                body: `grant_type=client_credentials&client_id=${accessTokenInfo.apiCredentials.client_id}&client_secret=${accessTokenInfo.apiCredentials.client_secret}`
            });

            const accessToken = await response.json();
            if (!accessToken || !accessToken.access_token) {
                throw new Error('Unable to obtain access token');
            }

            accessToken.expires_in = parseInt(accessToken.expires_in, 10);
            accessToken.obtainedAt = moment().toDate();
            accessToken.expiration = moment().add(accessToken.expires_in, 'seconds').toDate();
            this._accessTokens[accountId].accessToken = accessToken;

            this.clearAccessTokenInFuture(accountId);
            return accessToken;
        } catch (ex) {
            const err = this.raiseError(`An error occurred while attempting to contact the Brightcove Proxy. Please ensure the proxy is operational. ${ex}`);
            throw err;
        }
    }

    public invalidateAccessToken(accountId: string): void {
        if (!accountId) {
            throw new Error('Account Id must be specified.');
        }

        delete this._accessTokens[accountId];
    }

    protected raiseError(message: string, err?: Error): Error {

        const errorMessage = `${message} ${err && err.message ? err.message : ''}`;
        const outerErr = new Error(errorMessage);

        if (this._errorCallback) {
            const throwException = this._errorCallback(message, err);
            if (throwException === true) {
                throw outerErr;
            }
        } else {
            throw outerErr;
        }

        return outerErr;
    }

    private clearAccessTokenInFuture(accountId: string): void {

        if (!this._accessTokens[accountId]) {
            return;
        }

        let accessTokenInfo = this._accessTokens[accountId];
        if (!accessTokenInfo.accessToken || !accessTokenInfo.accessToken.expires_in) {
            return;
        }

        if (accessTokenInfo.clearAccessToken) {
            clearTimeout(accessTokenInfo.clearAccessToken);
        }

        accessTokenInfo.clearAccessToken = setTimeout(
            () => {
                accessTokenInfo.accessToken = undefined;
            },
            accessTokenInfo.accessToken.expires_in * 1000
        );
    }
}


