import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { Prompt } from 'react-router';
import { AccountFieldRow } from './AccountFieldRow';

import { BCGetCustomFieldsResponse, BCCustomField } from '@brightcove/video-cloud-sdk';
import { Account } from '../../services/sharepoint';
import { ConnectorContext, UserRole, ConnectorValidationResult } from '../../services/connector';

import { Spinner } from '../common';
import autobind from 'autobind-decorator';
import { isEqual, cloneDeep, find, pull } from 'lodash';

import './AccountView.css';

export class AccountView extends React.Component<AccountViewProps, AccountViewState> {
    constructor(props: AccountViewProps, context?: any) {
        super(props, context);

        this.state = {
            isDeletingAccount: false,
            isSavingAccount: false,
            isLoadingAccount: true,
            isLoadingCustomFields: false,
            showCustomFieldsModal: false,
            showDeleteModal: false,
        };
    }

    async componentDidMount() {

        const { connectorContext, validationResult } = this.props;
        const { spApiGateway } = connectorContext;

        if (validationResult.accountEmptyGroups.length === 0 && (!connectorContext.currentUserRole || connectorContext.currentUserRole !== UserRole.Author)) {
            window.location.hash = '#/';
        }

        // Get the brightcove accounts stored in the host web.
        const accountsList = spApiGateway.accountsList;
        const account = await accountsList.getItemById(this.props.accountId);

        this.setState({
            isLoadingAccount: false,
            account: cloneDeep(account),
            originalAccount: account
        });
    }

    @autobind
    async saveAccountChanges() {

        this.setState({
            isSavingAccount: true
        });

        const { account } = this.state;

        if (!account) {
            return;
        }

        const { connectorContext } = this.props;
        const { spApiGateway } = connectorContext;
        const accountsList = spApiGateway.accountsList;
        const updatedAccount = await accountsList.updateItem(this.props.accountId, account);

        // Update available accounts.
        await connectorContext.refreshAccounts();

        this.setState({
            isSavingAccount: false,
            account: cloneDeep(updatedAccount),
            originalAccount: updatedAccount
        });

        window.location.hash = '#/account-management';
    }

    @autobind
    deleteAccount() {
        this.setState({
            showDeleteModal: true
        });
    }

    @autobind
    deleteCancel() {
        this.setState({
            showDeleteModal: false
        });
    }

    @autobind
    async deleteConfirm() {

        this.setState({
            isDeletingAccount: true
        });

        const { connectorContext } = this.props;
        const { spApiGateway } = connectorContext;
        const accountsList = spApiGateway.accountsList;
        await accountsList.deleteItem(this.props.accountId);
        await connectorContext.refreshAccounts();
        window.location.hash = '#/account-management';
    }

    @autobind
    async customFieldsShow() {

        const { account } = this.state;

        if (!account) {
            return;
        }

        this.setState({
            isLoadingCustomFields: true,
            showCustomFieldsModal: true
        });

        const { connectorContext } = this.props;
        const { videosApi } = connectorContext.bcApiGateway;
        const customFields = await videosApi.getCustomFields(account.accountId);
        this.setState({
            isLoadingCustomFields: false,
            customFields: customFields,
            accountInEdit: cloneDeep(account)
        });
    }

    @autobind
    customFieldsCancel() {
        this.setState({
            showCustomFieldsModal: false,
            accountInEdit: undefined
        });
    }

    @autobind
    customFieldsHide() {

        let { account, accountInEdit } = this.state;
        if (!account || !accountInEdit) {
            return;
        }

        account.customFields = accountInEdit.customFields;
        this.setState({
            account,
            accountInEdit: undefined,
            showCustomFieldsModal: false
        });
    }

    @autobind
    handleCustomFieldCheckChange(e: React.ChangeEvent<HTMLInputElement>, customField: BCCustomField) {
        const { accountInEdit } = this.state;
        if (!accountInEdit) {
            return;
        }

        let existingCustomField = find(accountInEdit.customFields, { internalName: customField.id });

        if (!accountInEdit.customFields) {
            accountInEdit.customFields = [];
        }

        if (e.target.checked === true) {
            if (!existingCustomField) {
                accountInEdit.customFields.push({
                    internalName: customField.id,
                    displayName: customField.display_name,
                    required: customField.required
                });
            }
        } else {
            if (existingCustomField) {
                pull(accountInEdit.customFields, existingCustomField);
            }
        }

        this.setState({
            accountInEdit
        });
    }

    render() {
        const { connectorContext } = this.props;

        const {
            isDeletingAccount,
            isSavingAccount,
            isLoadingAccount,
            account,
            originalAccount,
            isLoadingCustomFields,
            customFields,
            accountInEdit,
        } = this.state;

        if (isDeletingAccount) {
            return (
                <Spinner loadingText="Deleting Account..." />
            );
        }

        if (isSavingAccount) {
            return (
                <Spinner loadingText="Saving Account..." />
            );
        }

        if (isLoadingAccount || !account) {
            return (
                <Spinner loadingText="Loading Account..." />
            );
        }

        const hasChanges = isEqual(account, originalAccount);

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li><a href="#/account-management">Account Management</a></li>
                    <li className="active">{account.title}</li>
                </ol>

                <div className="hideSection">
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <td>Property</td>
                                <td>Value</td>
                                <td>Description</td>
                            </tr>
                        </thead>
                        <tbody>
                            <AccountFieldRow
                                fieldName="Account Name"
                                fieldDescription="This field is used to specify the account name"
                                fieldValue={account.title}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, title: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="DefaultVideoPlayerId"
                                fieldDescription="This field is used to specify the default video player of the account"
                                fieldValue={account.defaultVideoPlayerId}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, defaultVideoPlayerId: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="DefaultPlaylistPlayerId"
                                fieldDescription="This field is used to specify the default playlist id of the account"
                                fieldValue={account.defaultPlaylistPlayerId}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, defaultPlaylistPlayerId: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="Account Id"
                                fieldDescription="The Brightcove Account Id"
                                fieldValue={account.accountId}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, accountId: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="Client Id"
                                fieldDescription="The Brightcove API Authentication Client Id"
                                fieldValue={account.clientId}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, clientId: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="Client Secret"
                                fieldDescription="The Brightcove API Authentication Client Secret"
                                fieldValue={account.clientSecret}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, clientSecret: newValue } }); }}
                            />
                        </tbody>
                    </table>
                    <hr />
                    {/* <h5 style={{ fontWeight: 'bold' }}>Custom Fields</h5>
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <td>Value (Brightcove Internal name)</td>
                                <td>Required</td>
                            </tr>
                        </thead>
                        <tbody>
                            {account.customFields && account.customFields.map((customField, i) =>
                                <tr key={i}>
                                    <td><span className="btn-primary btn-lg account mLinks">{customField.internalName}</span></td>
                                    <td><span className="btn-primary btn-lg account mLinks">{customField.required ? 'Yes' : 'No'}</span></td>
                                </tr>
                            )}
                        </tbody>
                    </table>

                    <div>
                        <Button bsStyle="default" onClick={this.customFieldsShow} disabled={!account.accountId} title={!account.accountId ? 'Please enter an account Id' : ''}>Edit Custom Fields</Button>
                    </div>

                    <hr /> */}

                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <td>Group</td>
                                <td>Value</td>
                            </tr>
                        </thead>
                        <tbody>
                            <AccountFieldRow
                                fieldName="Author's Group"
                                fieldValue={account.accountAuthorsGroupName}
                                fieldValueType="group"
                                connectorContext={connectorContext}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, accountAuthorsGroupName: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="Viewer's Group"
                                fieldValue={account.accountViewersGroupName}
                                fieldValueType="group"
                                connectorContext={connectorContext}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, accountViewersGroupName: newValue } }); }}
                            />
                        </tbody>
                    </table>
                    <div>
                        <hr />
                        <Button bsStyle={!hasChanges ? 'primary' : 'default'} bsSize="large" disabled={hasChanges} onClick={this.saveAccountChanges} style={{ margin: '2px' }}>Save Account</Button>
                        <Button bsStyle="warning" bsSize="large" onClick={this.deleteAccount} style={{ margin: '2px' }}>Delete Account</Button>
                        <Button bsStyle="default" bsSize="large" href="#/account-management" style={{ margin: '2px' }}>Cancel</Button>
                    </div>
                </div>

                <Modal show={this.state.showCustomFieldsModal} onHide={this.customFieldsCancel}>
                    <Modal.Header closeButton={true}>
                        <Modal.Title>Edit Custom Fields</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        {(isLoadingCustomFields || !customFields) &&
                            <Spinner loadingText="Loading Custom Fields..." />
                        }
                        {!isLoadingCustomFields && customFields && accountInEdit &&
                            <>
                                Below is the complete list of custom fields defines in Video Cloud for this account. To Make them available in SharePoint check the "Include" box.
                        <table className="table table-striped">
                                    <thead>
                                        <tr>
                                            <td>Video cloud custom Field</td>
                                            <td>Type</td>
                                            <td>Required</td>
                                            <td>Include</td>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {customFields.custom_fields.map((customField, i) =>
                                            <tr key={i}>
                                                <td>{customField.display_name}</td>
                                                <td>{customField.type === 'string' ? 'Text' : 'List'}</td>
                                                <td>{customField.required ? 'Yes' : 'No'}</td>
                                                <td>
                                                    <input
                                                        type="checkbox"
                                                        value={customField.id}
                                                        checked={find(accountInEdit.customFields, { internalName: customField.id }) ? true : false}
                                                        onChange={(e) => this.handleCustomFieldCheckChange(e, customField)}
                                                    />
                                                </td>
                                            </tr>
                                        )}
                                    </tbody>
                                </table>
                            </>
                        }
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={this.customFieldsHide} bsStyle="primary">Ok</Button>
                        <Button onClick={this.customFieldsCancel}>Cancel</Button>
                    </Modal.Footer>
                </Modal>

                <Modal show={this.state.showDeleteModal} onHide={this.deleteCancel}>
                    <Modal.Header closeButton={true}>
                        <Modal.Title>Confirm Account Deletion</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <h5>This account will be permanently deleted. Are you sure you want to proceed?</h5>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={this.deleteConfirm} bsStyle="danger">Delete Account</Button>
                        <Button onClick={this.deleteCancel}>Cancel</Button>
                    </Modal.Footer>
                </Modal>
                <Prompt
                    when={!hasChanges}
                    message="There are unsaved changes. Are you sure you wish to leave?"
                />
            </>
        );
    }
}

interface AccountViewProps {
    connectorContext: ConnectorContext;
    accountId: string;
    validationResult: ConnectorValidationResult;
}

interface AccountViewState {
    account?: Account;
    originalAccount?: Account;
    isDeletingAccount: boolean;
    isSavingAccount: boolean;
    isLoadingAccount: boolean;
    isLoadingCustomFields: boolean;
    customFields?: BCGetCustomFieldsResponse;
    accountInEdit?: Account;
    showCustomFieldsModal: boolean;
    showDeleteModal: boolean;
}


