import * as React from 'react';
import { Button } from 'react-bootstrap';
import * as Datetime from 'react-datetime';

export class DatePicker extends React.Component<DatePickerProps> {

    renderDateTime(props: any, openCalendar: React.MouseEventHandler<Button>, closeCalendar: Function) {
        return (
            <div className="input-group">
                <input className="form-control" {...props} />
                <div className="input-group-btn">
                    <Button onClick={openCalendar} disabled={props.disabled}><i className="glyphicon glyphicon-calendar" /></Button>
                </div>
            </div>
        );
    }

    render() {
        const { value, placeholder, onChange, disabled } = this.props;

        return (
            <Datetime
                value={value}
                onChange={onChange}
                timeFormat={false}
                // @ts-ignore
                renderInput={this.renderDateTime}
                inputProps={{ placeholder: placeholder, disabled: disabled }}
            />
        );
    }
}

export interface DatePickerProps {
    name?: string;
    value?: Date;
    placeholder?: string;
    onChange?: Datetime.EventOrValueHandler<React.ChangeEvent<any>>;
    disabled?: boolean;
}


