import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { CopyToClipboard } from '../common/CopyToClipboard';
import autobind from 'autobind-decorator';

import { BrightcoveExperience, BrightcoveExperienceProps, getDefaultBrightcoveExperienceProps, BrightcoveExperienceControls } from '../brightcove-experience';

import { BCExperience } from '@brightcove/video-cloud-sdk';
import { ConnectorContext } from '../../services/connector';

export class ExperienceEmbed extends React.Component<ExperienceEmbedProps, ExperienceEmbedState> {
    constructor(props: ExperienceEmbedProps, context?: any) {
        super(props, context);

        let bcExperienceProps = getDefaultBrightcoveExperienceProps();
        bcExperienceProps.sharepointVersion = props.connectorContext.spAddinContext.SPProductNumber;

        this.state = {
            bcExperienceProps
        };
    }

    @autobind
    handleExperiencePropsChanged(bcExperienceProps: Partial<BrightcoveExperienceProps>) {
        this.setState({
            bcExperienceProps
        });
    }

    render() {
        const { layoutMode, connectorContext, experience, labelClass, controlClass } = this.props;
        const { bcExperienceProps } = this.state;

        if (!experience) {
            return (
                <div className="text-center">
                    <i>An experience has not been selected. Please select an experience above to obtain the embed code.</i>
                </div>
            );
        }

        if (!connectorContext.selectedAccount) {
            return (
                <div className="text-center">
                    <i>An account has not been selected. Please select an account above.</i>
                </div>
            );
        }

        const experienceSharableUrl = `https://players.brightcove.net/${connectorContext.selectedAccount.accountId}/experience_${experience.id}/share.html`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(
            <BrightcoveExperience accountId={connectorContext.selectedAccount.accountId} experienceId={experience.id} experience={experience} {...bcExperienceProps as BrightcoveExperienceProps} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');

        let layoutSize = 'Responsive';
        if (experience.width) {
            layoutSize = `Fixed width of ${experience.width}px`;
        }

        if (experience.height) {
            layoutSize += ` and height of ${experience.height}px`;
        }

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Experience Name</label>
                    <div className={controlClass}>{experience.name}</div>
                </div>
                {layoutMode === 'Full' &&
                    <iframe className="col-md-12" src={experience.previewUrl} style={{ height: '400px' }} />
                }
                <div className="form-group">
                    <label className={labelClass}>Shareable URL</label>
                    <div className={controlClass}><a href={experienceSharableUrl} target="_blank">{experienceSharableUrl}</a></div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Experience-defined Layout Size</label>
                    <div className={controlClass}>{layoutSize}</div>
                </div>
                {layoutMode === 'Mini' &&
                    <>
                        <BrightcoveExperienceControls
                            connectorContext={connectorContext}
                            labelClass={labelClass}
                            controlClass={controlClass}
                            formControlClass="input-sm"
                            bcExperienceProps={bcExperienceProps}
                            onExperiencePropsChanged={this.handleExperiencePropsChanged}
                        />
                        <div className="form-group">
                            <label className={labelClass}>Brightcove Snippet (Paste to Script Editor web part or publishing zone):</label>
                            <div className={controlClass}>
                                {/* {!bcExperienceProps.overrideExperienceVideos && !bcExperienceProps.overrideHeight && !bcExperienceProps.overrideWidth &&
                                    <div>After your experience has been published once, additional published changes will be included in the existing embed code. You do not need to re-copy and paste the embed code into existing locations.</div>
                                }
                                {(bcExperienceProps.overrideExperienceVideos || bcExperienceProps.overrideHeight || bcExperienceProps.overrideWidth) &&
                                    <div>This experience has been customized. Any changes made will need to be re-copied and pasted into existing locations.</div>
                                } */}
                                <textarea id="embedCode" style={{ width: '100%', height: '80px' }} value={embedCode} spellCheck={false} onChange={() => null} />
                                <CopyToClipboard />
                            </div>
                        </div>
                    </>
                }
                {layoutMode === 'Full' &&
                    <>
                        <div className="row">
                            <div className="col-md-6">
                                <h4>Copy Code</h4>
                                <div>Copy the appropriate code below and paste it into your web page or app.</div>
                                <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={() => null} />
                                <CopyToClipboard />
                            </div>
                            <div className="col-md-6">
                                <BrightcoveExperienceControls
                                    connectorContext={connectorContext}
                                    labelClass={labelClass}
                                    controlClass={controlClass}
                                    formControlClass="input-sm"
                                    bcExperienceProps={bcExperienceProps}
                                    onExperiencePropsChanged={this.handleExperiencePropsChanged}
                                />
                            </div>
                        </div>
                    </>
                }
            </>
        );
    }
}

export interface ExperienceEmbedProps {
    connectorContext: ConnectorContext;
    experience?: BCExperience;
    layoutMode: 'Mini' | 'Full';
    labelClass?: string;
    controlClass?: string;
}

export interface ExperienceEmbedState {
    bcExperienceProps: Partial<BrightcoveExperienceProps>;
}


