import { Web, sp } from 'sp-pnp-js';
import { SharePointPageType, SPUtility } from '.';
import { startsWith } from 'lodash';

import * as URI from 'urijs';

const webPartXml = '<?xml version=\"1.0\" encoding=\"utf-8\"?>' +
    '<WebPart xmlns:xsi=\"http://www.w3.org/2001/XMLSchema-instance\"' +
    ' xmlns:xsd=\"http://www.w3.org/2001/XMLSchema\"' +
    ' xmlns=\"http://schemas.microsoft.com/WebPart/v2\">' +
    '<Title>My Web Part</Title><FrameType>Default</FrameType>' +
    '<Description>Use for formatted text, tables, and images.</Description>' +
    '<IsIncluded>true</IsIncluded><ZoneID></ZoneID><PartOrder>0</PartOrder>' +
    '<FrameState>Normal</FrameState><Height /><Width /><AllowRemove>true</AllowRemove>' +
    '<AllowZoneChange>true</AllowZoneChange><AllowMinimize>true</AllowMinimize>' +
    '<AllowConnect>true</AllowConnect><AllowEdit>true</AllowEdit>' +
    '<AllowHide>true</AllowHide><IsVisible>true</IsVisible><DetailLink /><HelpLink />' +
    '<HelpMode>Modeless</HelpMode><Dir>Default</Dir><PartImageSmall />' +
    '<MissingAssembly>Cannot import this Web Part.</MissingAssembly>' +
    '<PartImageLarge>/_layouts/images/mscontl.gif</PartImageLarge><IsIncludedFilter />' +
    '<Assembly>Microsoft.SharePoint, Version=13.0.0.0, Culture=neutral, ' +
    'PublicKeyToken=94de0004b6e3fcc5</Assembly>' +
    '<TypeName>Microsoft.SharePoint.WebPartPages.ContentEditorWebPart</TypeName>' +
    '<ContentLink xmlns=\"http://schemas.microsoft.com/WebPart/v2/ContentEditor\" />' +
    '<Content xmlns=\"http://schemas.microsoft.com/WebPart/v2/ContentEditor\">' +
    '<![CDATA[This is a first paragraph!<DIV>&nbsp;</DIV>And this is a second paragraph.]]></Content>' +
    '<PartStorage xmlns=\"http://schemas.microsoft.com/WebPart/v2/ContentEditor\" /></WebPart>';

/**
 * Contains utility methods associated with the Brightcove Embed Helper
 */
export class SPEmbedUtil {
    private readonly _addinWebUrl?: string;
    private readonly _hostWebUrl: string;
    private readonly _web: Web;

    private _doesEmbedHelperExist: boolean;

    constructor(hostWebUrl: string, addinWebUrl?: string) {

        this._addinWebUrl = addinWebUrl;
        this._hostWebUrl = hostWebUrl;

        if (!addinWebUrl) {
            this._web = new Web(hostWebUrl);
        } else {
            this._web = sp.crossDomainWeb(addinWebUrl, hostWebUrl);
        }
    }

    /**
     * Returns a value that indicates if the embed helper exists.
     */
    async doesEmbedHelperExist(): Promise<boolean> {
        if (this._doesEmbedHelperExist !== undefined) {
            return this._doesEmbedHelperExist;
        }

        const embedHelperServerRelativeUrl = URI(this._hostWebUrl + '/Documents/BrightcoveEmbedHelper.aspx').path();
        const embedHelper = this._web.getFileByServerRelativeUrl(embedHelperServerRelativeUrl);
        try {
            await embedHelper.getText();
            this._doesEmbedHelperExist = true;
            return true;
        } catch (ex) {
            this._doesEmbedHelperExist = false;
        }

        return this._doesEmbedHelperExist;
    }

    async getWebPartPageType(pageUrl: string): Promise<SharePointPageType | undefined> {
        const referrer = URI(pageUrl).path();
        if (!referrer || URI(referrer).equals(document.location.origin)) {
            return undefined;
        }

        const referrerServerRelativeUrl = new URI(referrer)
            .relativeTo(this._hostWebUrl)
            .toString();

        const file = await this._web.getFileByServerRelativeUrl(referrerServerRelativeUrl)
            .listItemAllFields
            .select('ContentTypeId,PublishingPageLayout')
            .get();

        let pageType = SharePointPageType.Other;

        if (startsWith(file.ContentTypeId, '0x010108')) {
            pageType = SharePointPageType.WikiPage;
        } else if (startsWith(file.ContentTypeId, '0x010109')) {
            pageType = SharePointPageType.WikiPage;
        } else if (file.PublishingPageLayout !== undefined) {
            pageType = SharePointPageType.PublishingPage;
        }

        return pageType;
    }

    async insertScriptEditorWebPart(webPartPageUrl: string): Promise<boolean> {
        // 1. determine the type of web part page.
        const pageType = await this.getWebPartPageType(webPartPageUrl);

        if (pageType !== SharePointPageType.WikiPage &&
            pageType !== SharePointPageType.PublishingPage &&
            pageType !== SharePointPageType.WebPartPage) {
            return false;
        }

        if (!this.doesEmbedHelperExist()) {
            return false;
        }

        if (!(window as any).SP || !(window as any).SP.ClientContext) {
            return false;
        }

        const webPartPageServerRelativeUrl = URI(webPartPageUrl).path();
        if (!webPartPageServerRelativeUrl || URI(webPartPageServerRelativeUrl).equals(document.location.origin)) {
            return false;
        }

        const referrerServerRelativeUrl = new URI(webPartPageServerRelativeUrl)
            .relativeTo(this._hostWebUrl)
            .toString();

        switch (pageType) {
            case SharePointPageType.WikiPage:
                return this.addWebPartToWikiPage(referrerServerRelativeUrl);
            case SharePointPageType.PublishingPage:
            case SharePointPageType.WebPartPage:
                return this.addWebPartToPage(referrerServerRelativeUrl);
            default:
                return false;
        }
    }

    async addWebPartToWikiPage(wikiPageServerRelativeUrl: string): Promise<boolean> {

        const context = new SP.ClientContext(this._addinWebUrl);
        // const factory = new SP.ProxyWebRequestExecutorFactory(connectorContext.spAddinContext.SPAppWebUrl);
        // context.set_webRequestExecutorFactory(factory);
        const appContextSite = new SP.AppContextSite(context, this._hostWebUrl);
        const referrerWeb = appContextSite.get_web();
        const file = referrerWeb.getFileByServerRelativeUrl(wikiPageServerRelativeUrl);

        const lwpm = file.getLimitedWebPartManager(SP.WebParts.PersonalizationScope.shared);
        const webPartDefinition = lwpm.importWebPart(webPartXml);
        const webPart = webPartDefinition.get_webPart();
        lwpm.addWebPart(webPart, 'wpz', 0);

        // string marker = String.Format(CultureInfo.InvariantCulture, "<div class=\"ms-rtestate-read ms-rte-wpbox\" contentEditable=\"false\"><div class=\"ms-rtestate-read {0}\" id=\"div_{0}\"></div><div style='display:none' id=\"vid_{0}\"></div></div>", new object[] { storageKey.ToString("D") });
        //     SPListItem item = page.Item;
        //     string content = item["PublishingPageContent"] as string;
        //     item["PublishingPageContent"] = marker + content;
        //     item.Update();
        await SPUtility.executePendingQuery(context);
        return false;
    }

    async addWebPartToPage(webPartPageServerRelativeUrl: string): Promise<boolean> {
        const referrer = URI(webPartPageServerRelativeUrl).path();
        if (!referrer || URI(referrer).equals(document.location.origin)) {
            return false;
        }

        const referrerServerRelativeUrl = new URI(referrer)
            .relativeTo(this._hostWebUrl)
            .toString();

        // Determine a zone to place our webpart in.
        const webparts = await this._web
            .getFileByServerRelativeUrl(referrerServerRelativeUrl)
            .getLimitedWebPartManager()
            .webparts
            .get();

        console.dir(webparts);
        return false;
    }

    static generateEmbedHelper(hostWebUrl: string, addInWebUrl?: string): Blob {
        if (!hostWebUrl) {
            throw Error('hostWebUrl must be specified.');
        }

        const activationPageData = require('raw-loader!../../assets/BrightcoveEmbedHelper.html') as string;
        const config = {
            responseOrigin: '*',
            trustedOriginAuthorities: [
                URI(hostWebUrl).origin()
            ],
        };

        if (addInWebUrl) {
            config.trustedOriginAuthorities.push(URI(addInWebUrl).origin());
        }

        const expression = `
            window.__brightcoveConfig = ${JSON.stringify(config, null, 4)};
            `;

        const activationPage = activationPageData.replace(/(\/\/ @\}-,--`--> Start BrightcoveConfig)([\s\S]*)(\/\/ @\}-,--`--> End BrightcoveConfig)/, `$1\n${expression}\n$3`);
        return new Blob([activationPage], { type: 'text/plain;charset=utf-8' });
    }
}


